import React from 'react'
import SimpleSectionComponent from '../simpleSectionComponent';
//import TextareaAutosize from '@material-ui/core/TextareaAutosize';


export default class InterestInput extends SimpleSectionComponent {

  renderInputs = () => {
    let interestInputs = [];
    for (let i = 1; i <= this.state.numInputs; i++) {
      interestInputs.push(
        <input
          style={styles.input}
          ref={i}
          key={i}
          value={this.state.values[i - 1]}
          onChange={(e) => this.updateValue('interest', e, i - 1)}
          placeholder={placeholders[i - 1]}
          onKeyDown={(e) => this.handleKeyDown('interest', i, e, i + 1)}
        />
      );
    }
    return interestInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
      </>
    )
  }
}

let styles = {
  input: {
    resize: 'none'
  }
}

let placeholders = [
  'Playing Games',
  'Passionate in Coding',
  'Designing',
  'Drawing',
  'Learning'
]