import React, { Component } from 'react';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import ProjectInput from '../components/complexSections/ProjectInput';
import CertificationInput from '../components/complexSections/CertificationInput';
import RecommendationInput from '../components/simpleSections/RecommendationInput';
import './resume13.css';

export default class Resume13P2 extends Component {
    renderExp = () => {
        return (
            <div className="resume13-section">
                <div className="resume13-section-title">{" "}</div>
                <span className="resume13-experience-inputs">
                    <ExperienceInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.experience}
                        inputVals={this.props.inputVal.experience}
                        ulClass="resume13-experience-ul"
                        textCol="resume13-text-color"
                        startNum={1}
                        maxSections={3}
                    />
                </span>
            </div>
        )
    }

    renderProject = () => {
        if (this.props.numInput.projects)
            return (<>
                <div className="resume13-section-title">PROJECT</div>
                <div className="resume13-input-section">
                    <span className="resume13-inputs">
                        <ProjectInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.project}
                            inputVals={this.props.inputVal.project}
                            ulClass="resume13-project-ul"
                            maxSections={1}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    renderCertificate = () => {
        if (this.props.numInput.certificates)
            return (<>
                <div className="resume13-section-title">CERTIFICATE</div>
                <div className="resume13-input-section">
                    <span className="resume13-inputs">
                        <CertificationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.certificate}
                            inputVals={this.props.inputVal.certificate}
                            ulClass="resume13-certificate-ul"
                            maxSections={1}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    renderRecommendation = () => {
        if (this.props.numInput.recommendations)
            return (<>
                <div className="resume13-section-title">PERSONAL REFERENCES</div>
                <div className="resume13-input-section">
                    <span className="resume13-inputs">
                        <RecommendationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.recommendation}
                            inputVals={this.props.inputVal.recommendation}
                            recommendationclass="resume13-recommendation-list"
                            maxInputs={3}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }
    render() {
        return (
            <div ref={this.props.refProp} className="resume13Root">
                <div className="resume13Main">
                    <div className="resume13-section-2-P2">

                        {this.renderExp()}

                        <div className="resume13-section">

                            {this.renderProject()}

                        </div>
                        <div className="resume13-section">

                            {this.renderCertificate()}

                        </div>
                        <div className="resume13-section">

                            {this.renderRecommendation()}

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}