import React from 'react'
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import { Button } from '@material-ui/core';
import CustomTextarea from '../customTextarea';
import ComplexSectionComponent from '../complexSectionComponent';


export default class ProjectInput extends ComplexSectionComponent {

  renderInputs = () => {
    let projectInputs = [];

    for (let i = 0; i < this.state.numInputs; i++) {
      projectInputs.push(
        <div key={i} style={styles.inputs}>
          <input
            value={this.state.values[i].name}
            onChange={(e) => this.updateValue('project', e, 'name', i)}
            placeholder="Name"
          />
          <div style={styles.dateInputs}>
            <input
              style={styles.dateInput}
              maxLength='10'
              value={this.state.values[i].start_date}
              onChange={(e) => this.updateValue('project', e, 'start_date', i)}
              placeholder="2012"
            />
            <input
              style={styles.dateInput}
              maxLength='10'
              value={this.state.values[i].end_date}
              onChange={(e) => this.updateValue('project', e, 'end_date', i)}
              placeholder="2016"
            />
          </div>
          <CustomTextarea
            updateTextarea={(description) => this.updateTextarea('project', description, 'description', i)}
            placeholder={placeholders}
            values={this.state.values[i].description}
            ulclass={this.props.ulClass}
          />
        </div>
      );
    }

    return projectInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
        <div style={styles.btns}>
          <Button onClick={() => this.incrementInputs('project')}><AddIcon fontSize="small" /></Button>
          <Button onClick={() => this.decrementInputs('project')}><RemoveIcon fontSize="small" /></Button>
        </div>
      </>
    )
  }
}


let styles = {
  btns: {
    display: 'flex'
  },
  inputs: {
    display: 'flex',
    flexDirection: 'column',
  },
  dateInput: {
    width: '100px',
    marginRight: '5px'
  }
}

let placeholders = {

}