import React from 'react'
import SimpleSectionComponent from '../simpleSectionComponent';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';


export default class RecommendationtInput extends SimpleSectionComponent {

    renderInputs = () => {
        let recommendationInputs = [];

        for (let i = 1; i <= this.state.numInputs; i++) {
            recommendationInputs.push(
                <TextareaAutosize
                    style={styles.input}
                    rowsMin={1}
                    maxLength={80}
                    ref={i}
                    key={i}
                    value={this.state.values[i - 1]}
                    onChange={(e) => this.updateValue('recommendation', e, i - 1)}
                    placeholder={"Recommendation " + [i]}
                    onKeyDown={(e) => this.handleKeyDown('recommendation', i, e, i + 1)}
                />
            );
        }
        return recommendationInputs;
    }

    render() {
        return (
            <>
                <div className={this.props.recommendationclass}>
                    {this.renderInputs()}
                </div>
            </>
        )
    }
}

let styles = {
    input: {
        resize: 'none'
    }
}