import React, { Component } from 'react';
import { Form, Button, Image } from "react-bootstrap";
import img from "../../../../assets/images/signup-1.png";
import google from "../../../../assets/images/social-logos/google-logo.png";
import TextField from '@material-ui/core/TextField';
//import Radio from '@material-ui/core/Radio';
//import RadioGroup from '@material-ui/core/RadioGroup';
//import FormControlLabel from '@material-ui/core/FormControlLabel';
//import CircularProgress from '@material-ui/core/CircularProgress';
import { connect } from 'react-redux';
import * as actions from '../../../../store/actions/auth';

import GoogleLogin from 'react-google-login';
import './SignUp.css'

const CLIENT_ID = '705583746303-1uakl3l1aadji14o03p8jdudetbebd7f.apps.googleusercontent.com';

class SignUp extends Component {

    state = {
        values: {
            'firstname': '',
            'lastname': '',
            'email': '',
            'password': '',
        },
        passwordError: false
    }

    login = (response) => {
        if (response.accessToken) {
            let firstname = response.profileObj.givenName
            let lastname = response.profileObj.familyName
            let email = response.profileObj.email
            let access_token = response.accessToken
            this.props.onAuthGoogle(firstname, lastname, email, access_token);
        }
    }

    loginFailure(response) {
        alert("Error!")
    }

    onChange = (field, e) => {
        let val = e.target.value;
        let curVal = this.state.values;
        curVal[field] = val;

        this.setState({
            values: curVal
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let value = this.state.values;
        if (value.password.length >= 8) {
            this.setState({
                passwordError: false
            })
            this.props.onAuth(value.firstname, value.lastname, value.email, value.email, value.password);
        } else {
            this.setState({
                passwordError: true
            })
        }
    }

    render() {
        let errorMessage = null;
        let error = this.props.error
        if (error) {
            if (error.message === 'Network Error') {
                return window.location.href = ('/error')
            } else if (error.response.data.detail === 'A user with that username already exists.') {
                errorMessage = (
                    <p className="signup-error">Email Address Already Exists</p>
                );
            }
        }

        return (
            <div className="signup-main">
                <Form className="signup-form" onSubmit={this.handleSubmit}>

                    <span className="signup-create-section">Create an account</span>
                    {/* <Button
                        className="google-button"
                        type="primary"
                        block="true" >
                        Sign Up with Google
                        <Image className="google-logo" src={google} />
                    </Button> */}
                    <GoogleLogin
                        render={renderProps => (
                            <button
                                type="button"
                                className="google-button"
                                onClick={renderProps.onClick} disabled={renderProps.disabled}>
                                <Image className="google-logo" src={google} />
                                Sign Up with Google
                            </button>
                        )}
                        clientId={CLIENT_ID}
                        onSuccess={this.login}
                        onFailure={this.loginFailure}
                        responseType='code,token'
                    />
                    <div className="signup-or-section">
                        <div className="strong-line-left"></div>
                        <span className="signup-or">or</span>
                        <div className="strong-line-right"></div>
                    </div>
                    {errorMessage}

                    <div className="signup-form-inputs">

                        <div className="name-inputs">

                            <div className="signup-margin first-name-input">
                                <TextField
                                    required
                                    label="First Name"
                                    variant="outlined"
                                    name="firstname"
                                    fullWidth
                                    onChange={(e) => this.onChange('firstname', e)}
                                />
                            </div>

                            <div className="signup-margin last-name-input">
                                <TextField
                                    required
                                    label="Last Name"
                                    variant="outlined"
                                    name="lastname"
                                    fullWidth
                                    onChange={(e) => this.onChange('lastname', e)}
                                />
                            </div>

                        </div>

                        {/* <div className="signup-margin username-input">
                            <TextField
                                variant="outlined"
                                label="Username"
                                fullWidth
                                name="username"
                                onChange={(e) => this.onChange('username', e)}
                            />
                        </div> */}

                        <div className="signup-margin email-input">
                            <TextField
                                required
                                variant="outlined"
                                label="Email Address"
                                fullWidth
                                name="email"
                                type="email"
                                onChange={(e) => this.onChange('email', e)}
                            />
                        </div>

                        <div className="signup-margin password-input">
                            <TextField
                                error={this.state.passwordError}
                                helperText="Password must be atleast 8 characters"
                                required
                                label="Create Password"
                                type="password"
                                variant="outlined"
                                fullWidth
                                name="password"
                                onChange={(e) => this.onChange('password', e)}
                            />
                        </div>

                        {/*<div className="radio-inputs">
                            <RadioGroup row aria-label="position" name="position" defaultValue="top">
                                <FormControlLabel
                                    value="frehser/intern"
                                    control={<Radio color="primary" />}
                                    label="Frehser / Intern"
                                />
                                <FormControlLabel
                                    value="experienced"
                                    control={<Radio color="primary" />}
                                    label="Experienced"
                                />
                                <FormControlLabel
                                    value="recruiter"
                                    control={<Radio color="primary" />}
                                    label="Recruiter" />
                            </RadioGroup>
                        </div>*/}

                    </div>

                    <div className="signup-submit-btn">
                        <Button bsPrefix="signup-submit" variant="warning" type="submit">
                            {this.props.loading ? "Please Wait..." : 'Sign Up'}
                        </Button>
                    </div>
                </Form>
                <div className="signup-image-section">
                    <Image className="signup-image" src={img} />
                </div>
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {
        loading: state.loading,
        error: state.error
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuthGoogle: (firstname, lastname, email, access_token) => dispatch(
            actions.authGoogleSignin(firstname, lastname, email, access_token)
        ),
        onAuth: (firstname, lastname, username, email, password) => dispatch(
            actions.authSignup(firstname, lastname, username, email, password)
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);