import { Component } from 'react';

class SingleSectionComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            numInputs: props.numInputs,
            values: props.inputVals
            /*{
                'company': '',
                'position': '',
                'start_date': '',
                'end_date': '',
                'description': ['', '', '', '', '', '']
            }*/
        }
    }

    updateValue = (sectionName, e, key) => {
        let inputValue = e.target.value;
        let currData = this.state.values;
        currData[key] = inputValue;

        this.setState({
            values: currData
        }, () => {
            this.props.updateSection(sectionName, this.state.values);
        });
    }

    updateTextarea = (sectionName, e, key) => {
        let inputValue = e;
        let currData = this.state.values;
        currData[key] = inputValue;

        this.setState({
            values: currData
        }, () => {
            this.props.updateSection(sectionName, this.state.values);
        });
    }
}

export default SingleSectionComponent;