import React, { Component } from 'react';
import { Modal, Container, Button } from 'react-bootstrap';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import './modal.css'

class AgreeModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checked: false
        }
    }
    handleChange = () => {
        this.setState({
            checked: !this.state.checked
        })
    }
    render() {
        return (
            <Modal style={{ 'backgroundColor': "#0000001a" }} show={this.props.open} onHide={() => this.props.close()} dialogClassName="agree-modal" >
                <Modal.Header style={styles.header} closeButton></Modal.Header>
                <Modal.Body >
                    <Container style={styles.container}>
                        <p style={styles.heading} >Please check the box below to confirm that you agree to comply with the Terms and Conditions.</p>
                        <RadioGroup row aria-label="position" name="position" defaultValue="top">
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        onChange={() => this.handleChange()} color="primary" />}
                                label="Yes. I have read the terms and conditions and agree to proceed with my consent."
                            />
                        </RadioGroup>
                        <div style={styles.buttonSection}>
                            <Button style={styles.button} onClick={this.props.submit} disabled={!this.state.checked}>
                                Download
                                </Button>
                        </div>
                    </Container>
                </Modal.Body>
            </Modal>
        );
    }
}

let styles = {
    header: {
        backgroundColor: "#fede00"
    },
    container: {
        marginLeft: 'auto',
        marginRight: 'auto'
    },
    heading: {
        fontSize: '16px',
        fontWeight: 700
    },
    buttonSection: {
        marginTop: '3%',
        display: 'flex',
        justifyContent: 'center',
        width: '100%'
    },
    button: {
        fontSize: '14px',
        backgroundColor: '#333333',
        border: 'none',
        padding: '0.5rem 0.8rem'
    }
}

export default AgreeModal;