import React, { Component } from 'react';
import AchievementInput from '../components/simpleSections/AchievementInput';
import SkillInput from '../components/simpleSections/SkillInput';
import ContactInput from '../components/simpleSections/ContactInput';
import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resume11.css';

export default class Resume11P1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <div className='resume11-section'>
                    <p className="resume11-section-title">INTERNSHIP / COURSE</p>
                    <span className="resume11-inputs">
                        <InternInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            numInputs={this.props.numInput.intern}
                            inputVals={this.props.inputVal.intern}
                            textCol="resume11-text-color"
                        />
                    </span>
                </div>
            )
        else
            return (
                <div className='resume11-section'>
                    <p className="resume11-section-title">RELEVANT EXPERIENCE</p>
                    <span className="resume11-inputs">
                        <ExperienceInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.experience}
                            inputVals={this.props.inputVal.experience}
                            ulClass="resume11-experience-ul"
                            textCol="resume11-text-color"
                            startNum={0}
                            maxSections={2}
                        />
                    </span>
                </div>
            )
    }
    render() {
        return (
            <div className="resume11Root">
                <div className="resume11Main">
                    <div className="resume11-resume11"></div>
                    <div className="resume11-resumeHeader">
                        <div className="resume11-image">
                            <label className="resume11-image-label">
                                <img className="resume11-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                <input className="resume11-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                            </label>
                        </div>

                        <div className="resume11-name-position">
                            <span className="resume11-capBox resume11-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <input
                                name="position"
                                className="resume11-capBox resume11-positionBox"
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />
                        </div>
                    </div>

                    <div className="resume11-section-2">
                        <div className="resume11-left-section">
                            <p className="resume11-section-title">PROFILE SUMMARY</p>
                            <TextareaAutosize
                                name="profile"
                                className="resume11-descriptionBox"
                                value={this.props.inputVal.description}
                                placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                rowsMin={8}
                                maxLength={380}
                                onChange={(e) => this.props.updateBasic("description", e)}
                            />

                            {/*Contact Inputs*/}
                            <div className="resume11-section">
                                <p className="resume11-section-title">HOW TO CONTACT ME</p>
                                <span className="resume11-inputs">
                                    <ContactInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        inputVals={this.props.inputVal}
                                    />
                                </span>
                            </div>

                            {/*Skill Inputs*/}
                            <div className="resume11-section">
                                <p className="resume11-section-title">SKILLS</p>
                                <span className="resume11-inputs">
                                    <SkillInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.skill}
                                        inputVals={this.props.inputVal.skill}
                                        maxInputs={6}
                                    />
                                </span>
                            </div>

                            {/* Interest Inputs */}
                            <div className="resume11-section">
                                <p className="resume11-section-title">ACHIEVEMENTS</p>
                                <span className="resume11-inputs">
                                    <AchievementInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.achievement}
                                        inputVals={this.props.inputVal.achievement}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>

                            {/* Interest Inputs */}
                            <div className="resume11-section">
                                <p className="resume11-section-title">INTERESTS</p>
                                <span className="resume11-inputs">
                                    <InterestInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.interest}
                                        inputVals={this.props.inputVal.interest}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>

                        </div>
                        <div className="resume11-right-section">

                            <p className="resume11-section-title">HIGHEST EDUCATION</p>
                            <span className="resume11-inputs">
                                <EducationInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    numInputs={this.props.numInput.education}
                                    inputVals={this.props.inputVal.education}
                                    textCol="resume11-text-color"
                                />
                            </span>

                            {this.renderExp()}

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}