import React from 'react';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import ProjectInput from '../components/complexSections/ProjectInput';
import CertificationInput from '../components/complexSections/CertificationInput';
import RecommendationInput from '../components/simpleSections/RecommendationInput';
import './resumeOne.css';

export default class ResumeOneP2 extends React.Component {

    renderExp = () => {
        return (
            <div>
                <ExperienceInput
                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                    updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                    numInputs={this.props.numInput.experience}
                    inputVals={this.props.inputVal.experience}
                    ulClass="resumeOne-experience-ul"
                    textCol={this.props.resumeOneText}
                    startNum={2}
                    maxSections={5}
                />
            </div>
        )
    }

    renderProject = () => {
        if (this.props.numInput.projects)
            return (<>
                <p className={this.props.resumeOneSectionTitle}>PROJECTS</p>
                <span className="resumeOne-inputs">
                    <ProjectInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.project}
                        inputVals={this.props.inputVal.project}
                        ulClass="resumeOne-project-ul"
                        maxSections={2}
                    />
                </span>
            </>
            )
        else
            return (null)
    }

    renderCertificate = () => {
        if (this.props.numInput.certificates)
            return (<>
                <p className={this.props.resumeOneSectionTitle}>CERTIFICATES</p>
                <span className="resumeOne-inputs">
                    <CertificationInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.certificate}
                        inputVals={this.props.inputVal.certificate}
                        ulClass="resumeOne-certificate-ul"
                        maxSections={2}
                    />
                </span>
            </>
            )
        else
            return (null)
    }

    renderRecommendation = () => {
        if (this.props.numInput.recommendations)
            return (<>
                <p className={this.props.resumeOneSectionTitle}>RECOMMENDATION</p>
                <span className="resumeOne-inputs">
                    <RecommendationInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.recommendation}
                        inputVals={this.props.inputVal.recommendation}
                        recommendationclass="resumeOne-recommendation-list"
                        maxInputs={3}
                    />
                </span>
            </>
            )
        else
            return (null)
    }

    render() {
        return (
            <>
                <div ref={this.props.refProp} className={"resumeOneP2Root " + this.props.resumeOneFont}>
                    <div className={"resumeOneMain " + this.props.resumeOneBorder}>

                        <div className="resumeOne-section-2">
                            <div className="resumeOneP2-left-section">
                                {this.renderProject()}

                                {this.renderCertificate()}

                                {this.renderRecommendation()}

                            </div>
                            <div className="resumeOneP2-right-section">

                                {this.renderExp()}

                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}