import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import img from '../assets/images/hiredin-logos/SVG/hiredin-footer-white.svg';
import { Link } from 'react-router-dom';

let styles = {
    headerParent: {
        display: 'flex',
        justifyContent: 'center',
        paddingTop: '18px',
        backgroundColor: '#221e1e'
    },
    img: {
        height: '50px'
    }
}

export default class customHeader extends Component {
    render() {
        return (
            <>
                <div className="editor-header" style={styles.headerParent}>
                    <Link to='/'><Image src={img} style={styles.img} /></Link>
                </div>
                {this.props.children}
            </>
        )
    }
}