import { Component } from 'react'
import submit from './submit';
import axios from 'axios';

export default class Master extends Component {

    state = {
        loading: false,
        downloading: false,
        error: false,
        paidUser: true,
        page2active: false,
        numInput: {
            'achievement': 3,
            'skill': 3,
            'interest': 3,
            'education': 1,
            'intern': true,
            'experience': 1,
            'projects': false,
            'project': 1,
            'certificates': false,
            'certificate': 1,
            'recommendations': false,
            'recommendation': 1
        },
        inputVal: {
            'user': '',
            'resume_number': '',
            'name': '',
            'resume_image': null,
            'age': '0',
            'position': '',
            'description': '',
            'email': '',
            'phone': '',
            'address': '',
            'url': '',
            'achievement': ['', '', '', '', ''],
            'skill': ['', '', '', '', ''],
            'interest': ['', '', '', '', ''],
            'education': {
                'institute': '',
                'degree': '',
                'course': '',
                'start_date': '',
                'end_date': '',
                'gpa': ''
            },
            'intern': {
                'company': '',
                'position': '',
                'start_date': '',
                'end_date': '',
                'description': ['', '', '', '', '', '']
            },
            'experience': [
                {
                    'company': '',
                    'position': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'company': '',
                    'position': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'company': '',
                    'position': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'company': '',
                    'position': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'company': '',
                    'position': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
            ],
            'project': [
                {
                    'name': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'name': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
                {
                    'name': '',
                    'start_date': '',
                    'end_date': '',
                    'description': ['', '', '', '', '', '']
                },
            ],
            'certificate': [
                {
                    'name': '',
                    'certifying_center': '',
                    'description': ['', '', '', '', '', ''],
                    'obtainment_date': ''
                },
                {
                    'name': '',
                    'certifying_center': '',
                    'description': ['', '', '', '', '', ''],
                    'obtainment_date': ''
                },
                {
                    'name': '',
                    'certifying_center': '',
                    'description': ['', '', '', '', '', ''],
                    'obtainment_date': ''
                }
            ],
            'recommendation': ['', '', '', '', '']
        }
    }

    buttontoggle = () => {
        this.setState({ page2active: !this.state.page2active }, this.scrollToPage2);
    }

    scrollToPage2() {
        if (this.state.page2active === true) {
            this.myRef.current.scrollIntoView({
                behavior: 'smooth',
                block: 'nearest',
            })
        }
    }

    updateImage = (e) => {
        let inputImg = e.target.files[0];
        let currData = this.state.inputVal;
        let reader = new FileReader();
        reader.readAsDataURL(inputImg)
        reader.onload = (e) => {
            inputImg = e.target.result
            currData['resume_image'] = inputImg
            this.setState({
                inputVal: currData
            })
        }
    }

    updateBasic = (sectionName, e) => {
        let inputValue = e.target.value;
        let currData = this.state.inputVal;
        currData[sectionName] = inputValue;

        this.setState({
            inputVal: currData
        });
    }

    updateState = (fieldName, value) => {
        let currVal = this.state.inputVal;
        currVal[fieldName] = value;

        this.setState({
            inputVal: currVal
        });
    }

    updateNuminputs = (sectionName, value) => {
        let currVal = this.state.numInput;
        currVal[sectionName] = value;

        this.setState({
            numInput: currVal
        });
    }

    addExperience = () => {
        let currState = this.state.numInput
        currState.intern = !this.state.numInput.intern
        currState.experience = 1
        this.setState({
            numInput: currState
        })
    }

    addOptional = (sectionName) => {
        let currState = this.state.numInput
        if (this.state.page2active) {
            currState[sectionName] = !this.state.numInput[sectionName]
        } else {
            this.setState({ page2active: true }, this.scrollToPage2)
            currState[sectionName] = !this.state.numInput[sectionName]
        }
        this.setState({
            numInput: currState
        })
    }

    submitForm = () => {
        // let currData = this.state.inputVal;
        // let user = localStorage.getItem('useremail')
        // let name = localStorage.getItem('fullname')
        // currData['user'] = user
        // currData['name'] = name

        // this.setState({
        //     inputVal: currData
        // })
        let result = submit(this.state)
        if (result === "Network Error") {
            this.setState({
                error: true
            }, console.log(result))
        }
    }

    recieveForm = (resume_num) => {
        this.setState({
            loading: true
        })
        let user = localStorage.getItem('useremail')
        let name = localStorage.getItem('fullname')
        axios.get('resume/api/resume/?user=' + user + '&resume_number=' + resume_num)
            .then(res => {
                let data = res.data.data
                let imgdata = res.data.image
                let prop = res.data.propData
                let old_state = this.state
                let currData = this.state.inputVal
                let currProp = this.state.numInput
                if (prop.internActive !== '') {
                    old_state.page2active = JSON.parse(prop.page2active)
                    currProp['intern'] = JSON.parse(prop.internActive)
                    currProp['projects'] = JSON.parse(prop.projectActive)
                    currProp['certificates'] = JSON.parse(prop.certificateActive)
                    currProp['recommendations'] = JSON.parse(prop.recommendationActive)

                    currProp['achievement'] = prop.achievementNum
                    currProp['skill'] = prop.skillNum
                    currProp['interest'] = prop.interestNum
                    currProp['experience'] = prop.experienceNum
                    currProp['project'] = prop.projectNum
                    currProp['certificate'] = prop.certificateNum
                    currProp['recommendation'] = prop.recommendationNum
                }
                if (imgdata !== '') {
                    currData['resume_image'] = 'data:image/jpeg;base64,' + imgdata
                }
                currData['user'] = user
                currData['resume_number'] = resume_num
                currData['name'] = name
                currData['position'] = data.position
                currData['description'] = data.profile
                currData['age'] = data.age
                currData['email'] = data.email
                currData['phone'] = data.phone
                currData['address'] = data.address
                currData['url'] = data.url
                if (data.achievements !== '') {
                    let achievement = data.achievements.split(",,")
                    for (let i = 0; i < achievement.length; i++) {
                        currData['achievement'][i] = achievement[i]
                    }
                }
                if (data.skills !== '') {
                    let skill = data.skills.split(",,")
                    for (let i = 0; i < skill.length; i++) {
                        currData['skill'][i] = skill[i]
                    }
                }
                if (data.interests !== '') {
                    let interest = data.interests.split(",,")
                    for (let i = 0; i < interest.length; i++) {
                        currData['interest'][i] = interest[i]
                    }
                }
                currData['education']['institute'] = data.education_institute
                currData['education']['degree'] = data.education_degree
                currData['education']['course'] = data.education_course
                currData['education']['start_date'] = data.education_start
                currData['education']['end_date'] = data.education_end
                if (data.education_score !== null) {
                    currData['education']['gpa'] = data.education_score
                } else {
                    currData['education']['gpa'] = 0
                }
                currData['intern']['company'] = data.intern_company
                currData['intern']['position'] = data.intern_position
                currData['intern']['start_date'] = data.intern_start
                currData['intern']['end_date'] = data.intern_end
                if (data.intern_description !== '') {
                    let intern_description = data.intern_description.split(",,")
                    for (let i = 0; i < intern_description.length; i++) {
                        currData['intern']['description'][i] = intern_description[i]
                    }
                }
                let exp_comp = [data.experience_company_1,
                data.experience_company_2,
                data.experience_company_3,
                data.experience_company_4,
                data.experience_company_5]
                for (let i = 0; i < exp_comp.length; i++) {
                    currData['experience'][i]['company'] = exp_comp[i]
                }
                let exp_pos = [data.experience_position_1,
                data.experience_position_2,
                data.experience_position_3,
                data.experience_position_4,
                data.experience_position_5]
                for (let i = 0; i < exp_pos.length; i++) {
                    currData['experience'][i]['position'] = exp_pos[i]
                }
                let exp_start = [data.experience_start_1,
                data.experience_start_2,
                data.experience_start_3,
                data.experience_start_4,
                data.experience_start_5]
                for (let i = 0; i < exp_start.length; i++) {
                    currData['experience'][i]['start_date'] = exp_start[i]
                }
                let exp_end = [data.experience_end_1,
                data.experience_end_2,
                data.experience_end_3,
                data.experience_end_4,
                data.experience_end_5]
                for (let i = 0; i < exp_end.length; i++) {
                    currData['experience'][i]['end_date'] = exp_end[i]
                }
                if (data.experience_description_1 !== '') {
                    let exp_desc_1 = data.experience_description_1.split(",,")
                    currData['experience'][0]['description'] = exp_desc_1
                    let exp_desc_2 = data.experience_description_2.split(",,")
                    currData['experience'][1]['description'] = exp_desc_2
                    let exp_desc_3 = data.experience_description_3.split(",,")
                    currData['experience'][2]['description'] = exp_desc_3
                    let exp_desc_4 = data.experience_description_4.split(",,")
                    currData['experience'][3]['description'] = exp_desc_4
                    if (data.experience_description_5 !== null) {
                        let exp_desc_5 = data.experience_description_5.split(",,")
                        currData['experience'][4]['description'] = exp_desc_5
                    }
                }
                let proj_name = [data.project_name_1, data.project_name_2, data.project_name_3]
                for (let i = 0; i < proj_name.length; i++) {
                    currData['project'][i]['name'] = proj_name[i]
                }
                let proj_start = [data.project_start_1, data.project_start_2, data.project_start_3]
                for (let i = 0; i < proj_start.length; i++) {
                    currData['project'][i]['start_date'] = proj_start[i]
                }
                let proj_end = [data.project_end_1, data.project_end_2, data.project_end_3]
                for (let i = 0; i < proj_end.length; i++) {
                    currData['project'][i]['end_date'] = proj_end[i]
                }
                if (data.project_description_1 !== '') {
                    let proj_desc_1 = data.project_description_1.split(",,")
                    currData['project'][0]['description'] = proj_desc_1
                    let proj_desc_2 = data.project_description_2.split(",,")
                    currData['project'][1]['description'] = proj_desc_2
                    let proj_desc_3 = data.project_description_3.split(",,")
                    currData['project'][2]['description'] = proj_desc_3
                }
                let certify_name = [data.certificate_name_1, data.certificate_name_2, data.certificate_name_3]
                for (let i = 0; i < certify_name.length; i++) {
                    currData['certificate'][i]['name'] = certify_name[i]
                }
                let certify_center = [data.certifying_center_1, data.certifying_center_2, data.certifying_center_3]
                for (let i = 0; i < certify_center.length; i++) {
                    if (certify_center[i] !== null)
                        currData['certificate'][i]['certifying_center'] = certify_center[i]
                }
                let certify_date = [data.certificate_obtainment_date_1, data.certificate_obtainment_date_2, data.certificate_obtainment_date_3]
                for (let i = 0; i < certify_date.length; i++) {
                    currData['certificate'][i]['obtainment_date'] = certify_date[i]
                }
                if (data.certificate_description_1 !== '') {
                    let certify_desc_1 = data.certificate_description_1.split(",,")
                    currData['certificate'][0]['description'] = certify_desc_1
                }
                if (data.certificate_description_2 !== '') {
                    let certify_desc_2 = data.certificate_description_2.split(",,")
                    currData['certificate'][1]['description'] = certify_desc_2
                }
                if (data.certificate_description_3 !== '') {
                    let certify_desc_3 = data.certificate_description_3.split(",,")
                    currData['certificate'][2]['description'] = certify_desc_3
                }
                if (data.recommendations !== null) {
                    let recommend = data.recommendations.split(",,")
                    for (let i = 0; i < recommend.length; i++) {
                        currData['recommendation'][i] = recommend[i]
                    }
                }
                this.setState({
                    inputVal: currData,
                    numInput: currProp,
                    loading: false
                })
            }).catch(err => {
                console.log(err)
                this.setState({
                    error: true
                })
            })
    }
}