import React, { Component } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import './Loader.css'

class Loader extends Component {
    state = {}
    render() {
        return (
            <div className="loader">
                <CircularProgress
                    color='inherit'
                />
            </div>
        );
    }
}

export default Loader;