import React, { Component } from 'react';
import { Form, Button, Image } from "react-bootstrap";
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import * as actions from '../../../../store/actions/auth';
import google from "../../../../assets/images/social-logos/google-logo.png";
import GoogleLogin from 'react-google-login';
import './Login.css'

const CLIENT_ID = '705583746303-1uakl3l1aadji14o03p8jdudetbebd7f.apps.googleusercontent.com';

class Login extends Component {

    state = {
        values: {
            'email': '',
            'password': ''
        }
    }

    login = (response) => {
        if (response.accessToken) {
            let firstname = response.profileObj.givenName
            let lastname = response.profileObj.familyName
            let email = response.profileObj.email
            let access_token = response.accessToken
            this.props.onAuthGoogle(firstname, lastname, email, access_token);
        }
    }

    loginFailure(response) {
        alert("Error!")
    }

    onChange = (field, e) => {
        let val = e.target.value;
        let curVal = this.state.values;
        curVal[field] = val;

        this.setState({
            values: curVal
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let value = this.state.values;
        this.props.onAuth(value.email, value.password);
    }

    render() {
        let errorMessage = null;
        let error = this.props.error
        if (error) {
            if(error.message === 'Network Error'){
                return window.location.href = ('/error')
            }else{
                switch(error.response.status){
                    case (404): 
                    {
                        errorMessage = (
                            <p className="signup-error">Email Address Doesn't Exist</p>
                        );
                        break;
                    }
                    case (401): 
                    {
                        errorMessage = (
                            <p className="signup-error">Invalid Password. Please try again.</p>
                        );
                        break;
                    }
                    default: 
                    {
                        errorMessage = null
                    }
                }
            }
        }

        return (
            <div className="login-main">
                <Form className="login-form" onSubmit={this.handleSubmit}>
                    <div className="welcome-head">Welcome Back !</div>
                    <GoogleLogin
                        render={renderProps => (
                            <button
                                type="button"
                                className="google-button"
                                onClick={renderProps.onClick} disabled={renderProps.disabled}>
                                <Image className="google-logo" src={google} />
                                Login with Google
                            </button>
                        )}
                        clientId={CLIENT_ID}
                        onSuccess={this.login}
                        onFailure={this.loginFailure}
                        responseType='code,token'
                    />


                    <div className="login-or-section">
                        <div className="strong-line-left"></div>
                        <span className="login-or">or</span>
                        <div className="strong-line-right"></div>
                    </div>
                    {errorMessage}

                    <div className="login-margin login-email-input">
                        <TextField
                            required
                            label="Email Address"
                            variant="outlined"
                            name='email'
                            onChange={(e) => this.onChange('email', e)}
                            fullWidth
                        />
                    </div>

                    <div className="login-margin login-password-input">
                        <TextField
                            required
                            label="Password"
                            type="password"
                            variant="outlined"
                            name="password"
                            onChange={(e) => this.onChange('password', e)}
                            fullWidth
                        />
                    </div>

                    <a href='/register'>Forgot your password ?</a>

                    <div className="login-submit-btn">
                        <Button variant="warning" type="submit">
                            {this.props.loading ? 'Please Wait...' : 'Log In'}
                        </Button>
                    </div>
                </Form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        loading: state.loading,
        error: state.error
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuthGoogle: (firstname, lastname, email, access_token) => dispatch(
            actions.authGoogleSignin(firstname, lastname, email, access_token)
        ),
        onAuth: (email, password) => dispatch(
            actions.authLogin(email, password)
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);