import React, { Component } from 'react';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';

class CustomTextarea extends Component {
    constructor(props) {
        super(props)
        this.state = {
            numInputs: 1,
            totalInputs: 3,
            description: props.values
            // description: props['', '', '', '', '', '']
        }
    }

    onchange = (e, i) => {
        let inputValue = e.target.value;
        let currData = this.state.description;
        currData[i] = inputValue;

        this.setState({
            description: currData
        }, () => {
            this.props.updateTextarea(this.state.description);
        });
    }

    handleKeyDown(index, e, nextValue) {
        var KeyID = e.keyCode;
        switch (KeyID) {
            case 13:
                e.preventDefault();
                if (index === this.state.totalInputs) {
                    if (this.state.totalInputs < 3)
                        this.setState({
                            totalInputs: this.state.totalInputs + 1
                        }, () => this.refs[nextValue].focus())
                }
                else {
                    this.refs[nextValue].focus();
                }
                break;
            case 8:
                if (e.target.value === '' && index === this.state.totalInputs) {
                    if (this.state.totalInputs > 1)
                        this.setState({
                            totalInputs: this.state.totalInputs - 1
                        },
                            this.state.totalInputs !== 1 ?
                                this.refs[index - 1].focus() : null
                        )
                }
                break;
            default:
                break;
        }
    }

    renderTextarea = () => {
        let textareas = []
        for (let i = this.state.numInputs; i <= this.state.totalInputs; i++) {
            textareas.push(
                <li key={i} className={this.props.ulclass}>
                    <TextareaAutosize
                        className={this.props.ulclass}
                        style={styles.input}
                        rowsMin={1}
                        maxLength={160}
                        placeholder={this.props.placeholder[i - 1]}
                        ref={i}
                        value={this.state.description[i - 1]}
                        onChange={(e) => this.onchange(e, i - 1)}
                        onKeyDown={(e) => this.handleKeyDown(i, e, i + 1)}
                    />
                </li>
            )
        }
        return textareas;
    }

    render() {
        return (
            <ul className={this.props.ulclass} style={styles.inputs}>
                {this.renderTextarea()}
            </ul>
        );
    }
}

export default CustomTextarea;

let styles = {
    inputs: {
        backgroundColor: 'inherit',
        display: 'flex',
        flexDirection: 'column'
    },
    input: {
        width: '100%',
        resize: 'none',
        display: 'block'
    }
}