import React from 'react';
import ResumeOneP1 from './resumeOneP1';
import ResumeOneP2 from './resumeOneP2';
import Master from '../master';
import Editor from '../../editorComponents/editor';

export default class resumeOneMain extends Master {

    constructor(props) {
        super(props)
        this.myRef = React.createRef();
        this.state = {
            ...this.state,
            border: 'resumeOne-orange-color',
            text: 'resumeOne-orange-text',
            section: 'resumeOne-orange-section',
            font: 'resumeOne-montserrat'
        }
    }

    componentDidMount = () => {
        let resume_num = '17'
        this.recieveForm(resume_num)
    }

    updateClass = (value) => {
        this.setState({
            border: value.border,
            text: value.text,
            section: value.section,
            font: value.font
        })
    }

    incrementExpInputs = () => {
        let currState = this.state.numInput
        if (this.state.numInput.experience < 5) {
            if (this.state.numInput.experience === 2) {
                this.setState({ page2active: true }, this.scrollToPage2)
                currState.experience = this.state.numInput.experience + 1
                this.setState({ numInput: currState })
            } else {
                currState.experience = this.state.numInput.experience + 1
                this.setState({ numInput: currState })
            }
        }
    }

    decrementExpInputs = () => {
        let currState = this.state.numInput
        if (this.state.numInput.experience > 1) {
            currState.experience = this.state.numInput.experience - 1
            this.setState({ numInput: currState })
        }
    }

    render() {
        return (
            <>
                <Editor
                    submitForm={() => this.submitForm()}
                    buttontoggle={() => this.buttontoggle()}
                    addExperience={() => this.addExperience()}
                    addOptional={(sectionName) => this.addOptional(sectionName)}
                    incrementExpInputs={() => this.incrementExpInputs()}
                    decrementExpInputs={() => this.decrementExpInputs()}
                    page2active={this.state.page2active}
                    paidUser={this.state.paidUser}
                    intern={this.state.numInput.intern}
                    borderColor1="resumeOne-orange-color"
                    borderColor2="resumeOne-green-color"
                    textColor1="resumeOne-orange-text"
                    textColor2="resumeOne-green-text"
                    sectionColor1="resumeOne-orange-section"
                    sectionColor2="resumeOne-green-section"
                    font1="resumeOne-montserrat"
                    font2="resumeOne-cursive"
                    buttonColor1="#FF7900"
                    buttonColor2="#b93aa8"
                    updateClass={(value) => this.updateClass(value)}
                    resumePageOne={
                        <ResumeOneP1
                            resumeOneBorder={this.state.border}
                            resumeOneText={this.state.text}
                            resumeOneSectionTitle={this.state.section}
                            resumeOneFont={this.state.font}
                            updateImage={(e) => this.updateImage(e)}
                            updateBasic={(fieldname, e) => this.updateBasic(fieldname, e)}
                            updateState={(fieldname, value) => this.updateState(fieldname, value)}
                            updateNuminputs={(sectionName, value) => this.updateNuminputs(sectionName, value)}
                            numInput={this.state.numInput}
                            inputVal={this.state.inputVal}
                        />}
                    resumePageTwo={
                        <ResumeOneP2
                            resumeOneBorder={this.state.border}
                            resumeOneText={this.state.text}
                            resumeOneSectionTitle={this.state.section}
                            resumeOneFont={this.state.font}
                            updateState={(fieldname, value) => this.updateState(fieldname, value)}
                            updateNuminputs={(sectionName, value) => this.updateNuminputs(sectionName, value)}
                            numInput={this.state.numInput}
                            inputVal={this.state.inputVal}
                            refProp={this.myRef}
                        />}
                />
            </>
        );
    }
}