import React from 'react'
import Header from './Header/Header';
import Footer from './Footer/Footer';
import './Root.css'

export default class Root extends React.Component {

    /*
        !!!WARNING!!! 
        {{ 
            Importing a styling(css) file will make style absolute for div.
            The actual style comes from the parent component i.e Router(App.js). 
        }}
        
        This is just a basic skeleton of the webpage.
        The component passed as prop will be rendered between the Header and Footer.
    */

    state = {
        backgroundColor: this.props.color ? '#e9e5e5' : '#10aad8',
        boxShadow: 'none',
        headerLogo: require('../../assets/images/hiredin-logos/SVG/hiredin-header-black.svg'),
        paddingTop: '15px',
        paddingBottom: '10px',
        textColor: 'black'
    }

    /*
        This function listens to scroll event.
        Switches between two states when a page is scrolled more than 0 pixel
        It Helps switching between the two designs of header.
    */
    listenScrollEvent = e => {
        if (window.scrollY > 0) {
            this.setState({
                backgroundColor: '#242424',
                boxShadow: '0px 10px 8px -3px rgba(0,0,0,0.5)',
                headerLogo: require('../../assets/images/hiredin-logos/SVG/hiredin-footer-white.svg'),
                paddingTop: '0px',
                paddingBottom: '0px',
                textColor: 'white'
            });
        }
        else {
            this.setState({
                backgroundColor: this.props.color ? '#e9e5e5' : '#10aad8',
                boxShadow: 'none',
                headerLogo: require('../../assets/images/hiredin-logos/SVG/hiredin-header-black.svg'),
                paddingTop: '15px',
                paddingBottom: '10px',
                textColor: 'black'
            });
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenScrollEvent)
    }

    render() {
        return (
            <div className="root-background" style={{ 'backgroundColor': this.props.color }}>
                <Header
                    backgroundColor={this.state.backgroundColor}
                    headerLogo={this.state.headerLogo}
                    boxShadow={this.state.boxShadow}
                    paddingTop={this.state.paddingTop}
                    paddingBottom={this.state.paddingBottom}
                    textColor={this.state.textColor}
                />
                {this.props.children}
                <Footer />
            </div>
        );
    }
}