import React, { Component } from 'react'
import TextareaAutosize from '@material-ui/core/TextareaAutosize';

export default class ContactInput extends Component {

  constructor(props) {
    super(props)
    this.state = {
      values: props.inputVals
      // 'phone': this.props.inputVals.phone,
      // 'email': '',
      // 'portfolio_url': '',
      // 'address': ''
    }
  }

  updateSection = (e, key) => {
    let value = e.target.value;
    let currData = this.state.values
    currData[key] = value
    this.setState({
      values: currData
    }, () => {
      this.props.updateSection(key, value);
    });
  }


  renderInputs = () => {
    let inputList = [];
    for (var i = 0; i < 2; i++) {
      let key = data[i].key;
      inputList.push(
        <input
          key={i}
          name={data[i]['key']}
          value={this.state.values[key]}
          type={data[i]['type']}
          maxLength={data[i]['max']}
          onChange={(e) => this.updateSection(e, key)}
          placeholder={data[i]['example']}
        />
      );
    }
    return inputList;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
        <TextareaAutosize
          style={styles.textarea}
          rowsMin={1}
          rowsMax={2}
          maxLength={data[2].max}
          key={data[2].key}
          value={this.state.values[data[2].key]}
          type={data[2]['type']}
          onChange={(e) => this.updateSection(e, data[2].key)}
          placeholder={data[2]['example']}
        />
        <TextareaAutosize
          style={styles.textarea}
          rowsMin={1}
          rowsMax={2}
          maxLength={data[3].max}
          key={data[3].key}
          value={this.state.values[data[3].key]}
          type={data[3]['type']}
          onChange={(e) => this.updateSection(e, data[3].key)}
          placeholder={data[3]['example']}
        />
      </>
    )
  }
}

let styles = {
  textarea: {
    "resize": "none",
    "display": "block",
    "overflow": "hidden"
  }
}

const data = [
  {
    'name': 'Email',
    'example': 'john@doe.com',
    'key': 'email',
    'type': 'text',
    'max': 30
  },
  {
    'name': 'Contact',
    'example': '9876548273',
    'key': 'phone',
    'type': 'text',
    'max': 20
  },
  {
    'name': 'Portfolio URL',
    'example': 'linkedin.com/in/john-doe',
    'key': 'url',
    'type': 'text',
    'max': 50
  },
  {
    'name': 'Address',
    'example': '481 Baker Street, CA',
    'key': 'address',
    'type': 'text',
    'max': 60
  }
]