import React, { Component } from 'react';
import AchievementInput from '../components/simpleSections/AchievementInput';
import SkillInput from '../components/simpleSections/SkillInput';
import ContactInput from '../components/simpleSections/ContactInput';
import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resumeFive.css';

export default class ResumeFiveP1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <div className='resumeFive-section'>
                    <p className="resumeFive-right-section-title">INTERNSHIP / COURSE</p>
                    <span className="resumeFive-inputs">
                        <InternInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            numInputs={this.props.numInput.intern}
                            inputVals={this.props.inputVal.intern}
                            textCol="resumeFive-text-color"
                        />
                    </span>
                </div>
            )
        else
            return (
                <div className='resumeFive-section'>
                    <p className="resumeFive-right-section-title">WORK HISTORY</p>
                    <span className="resumeFive-inputs">
                        <ExperienceInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.experience}
                            inputVals={this.props.inputVal.experience}
                            ulClass="resumeFive-experience-ul"
                            textCol="resumeFive-text-color"
                            startNum={0}
                            maxSections={2}
                        />
                    </span>
                </div>
            )
    }
    render() {
        return (
            <div className="resumeFiveRoot">
                <div className="resumeFiveMain">
                    <div className="resumeFive-resumeHeader">
                        <div className="resumeFive-image">
                            <label className="resumeFive-image-label">
                                <img className="resumeFive-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                <input className="resumeFive-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                            </label>
                        </div>

                        <div className="resumeFive-name-position">
                            <span className="resumeFive-capBox resumeFive-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <input
                                name="position"
                                className="resumeFive-capBox resumeFive-positionBox"
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />
                            <span className="resumeFive-inputs">
                                <ContactInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    inputVals={this.props.inputVal}
                                />
                            </span>
                        </div>
                    </div>

                    <div className="resumeFive-section-2">
                        <div className="resumeFive-left-section">
                            <p className="resumeFive-section-title">PERSONAL PROFILE</p>
                            <TextareaAutosize
                                name="profile"
                                className="resumeFive-descriptionBox"
                                value={this.props.inputVal.description}
                                placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                rowsMin={8}
                                maxLength={380}
                                onChange={(e) => this.props.updateBasic("description", e)}
                            />

                            {/*Contact Inputs*/}
                            {/* <div className="resumeFive-section">
                                <p className="resumeFive-section-title">CONTACT INFO</p>
                                <span className="resumeFive-inputs-left">
                                    <ContactInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        inputVals={this.props.inputVal}
                                    />
                                </span>
                            </div> */}

                            {/*Skill Inputs*/}
                            <div className="resumeFive-section">
                                <p className="resumeFive-section-title">SKILLS AND ABILITIES</p>
                                <span className="resumeFive-inputs-left">
                                    <SkillInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.skill}
                                        inputVals={this.props.inputVal.skill}
                                        maxInputs={6}
                                    />
                                </span>
                            </div>

                            {/* Interest Inputs */}
                            <div className="resumeFive-section">
                                <p className="resumeFive-section-title">INTERESTS</p>
                                <span className="resumeFive-inputs-left">
                                    <InterestInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.interest}
                                        inputVals={this.props.inputVal.interest}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>

                            <div className="resumeFive-section">
                                <p className="resumeFive-section-title">ACHIEVEMENTS</p>
                                <span className="resumeFive-inputs-left">
                                    <AchievementInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.achievement}
                                        inputVals={this.props.inputVal.achievement}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>
                        </div>
                        <div className="resumeFive-right-section">

                            <p className="resumeFive-right-section-title">ACADEMIC HISTORY</p>
                            <span className="resumeFive-inputs">
                                <EducationInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    numInputs={this.props.numInput.education}
                                    inputVals={this.props.inputVal.education}
                                    textCol="resumeFive-text-color"
                                />
                            </span>

                            {this.renderExp()}

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}