import React from 'react';
// import AddIcon from '@material-ui/icons/Add';
// import RemoveIcon from '@material-ui/icons/Remove';
// import { Button } from '@material-ui/core';
import CustomTextarea from '../customTextarea';
import ComplexSectionComponent from '../complexSectionComponent';

export default class ExperienceInput extends ComplexSectionComponent {

    static getDerivedStateFromProps(props) {
        return { numInputs: props.numInputs }
    }

    renderInputs = () => {
        let experienceInputs = [];
        for (let i = this.props.startNum; i < this.state.numInputs && i < this.props.maxSections; i++) {
            experienceInputs.push(
                <div key={i} style={styles.inputs}>

                    <input
                        style={styles.position}
                        value={this.state.values[i].position}
                        onChange={(e) => this.updateValue('experience', e, 'position', i)}
                        placeholder="Digital Strategist"
                    />
                    <input
                        value={this.state.values[i].company}
                        className={this.props.textCol}
                        onChange={(e) => this.updateValue('experience', e, 'company', i)}
                        placeholder="Web Kandi Services, Co."
                    />

                    <div style={styles.dateInputs}>
                        <input
                            style={styles.dateInput}
                            maxLength='10'
                            value={this.state.values[i].start_date}
                            onChange={(e) => this.updateValue('experience', e, 'start_date', i)}
                            placeholder="2012"
                        />
                        <input
                            style={styles.dateInput}
                            maxLength='10'
                            value={this.state.values[i].end_date}
                            onChange={(e) => this.updateValue('experience', e, 'end_date', i)}
                            placeholder="2016"
                        />
                    </div>
                    <CustomTextarea
                        updateTextarea={(description) => this.updateTextarea('experience', description, 'description', i)}
                        placeholder={placeholders}
                        values={this.state.values[i].description}
                        ulclass={this.props.ulClass}
                    />
                </div>
            );
        }
        return experienceInputs;
    }

    render() {
        return (
            <>
                {this.renderInputs()}
                {/* <div style={styles.btns}>
                    <Button onClick={() => this.incrementInputs('experience')}><AddIcon fontSize="small" /></Button>
                    <Button onClick={() => this.decrementInputs('experience')}><RemoveIcon fontSize="small" /></Button>
                </div> */}
            </>
        )
    }
}

let styles = {
    btns: {
        display: 'flex'
    },
    inputs: {
        display: 'flex',
        flexDirection: 'column',
        marginBottom: "10px",
    },
    position: {
        fontSize: '15px',
        fontWeight: '500',
    },
    textarea: {
        minHeight: "38px",
        resize: "none",
        marginTop: "10px",
    },
    dateInputs: {
        display: 'flex'
    },
    dateInput: {
        width: '100px',
        marginRight: '10px'
    }
}

let placeholders = [
    'Redesigned and published web pages using the agile framework to incorporate SEO keyword',
    'Presented key findings to clients and internal account teams',
    'Planned and executed the program management tasks for the automated web content migration',
    'Develops, implements and manages marketing campaigns for various clients',
    'Works with graphic designers for web design and digital storytelling',
    'Planned and executed social media campaigns',
    // 'Describe your experience',
    // 'Describe your experience',
    // 'Describe your experience'
]