import React, { Component } from 'react';
import SkillInput from '../components/simpleSections/SkillInput';
//import ContactInput from '../components/simpleSections/ContactInput';
// import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import mail from '../../assets/images/template-icons/mail.svg'
import home from '../../assets/images/template-icons/home.svg'
import telephone from '../../assets/images/template-icons/telephone.svg'
import globe from '../../assets/images/template-icons/globe.svg'
import './resume13.css';

export default class Resume13P1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <>
                    <div className="resume13-section-title">INTERNSHIP / COURSE</div>
                    <div className="resume13-input-section">
                        <span className="resume13-inputs">
                            <InternInput
                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                numInputs={this.props.numInput.intern}
                                inputVals={this.props.inputVal.intern}
                                textCol="resume13-text-color"
                            />
                        </span>
                    </div>
                </>
            )
        else
            return (
                <>
                    <div className="resume13-section-title">CAREER</div>
                    <div className="resume13-input-section">
                        <span className="resume13-inputs">
                            <ExperienceInput
                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                numInputs={this.props.numInput.experience}
                                inputVals={this.props.inputVal.experience}
                                ulClass="resume13-experience-ul"
                                textCol="resume13-text-color"
                                startNum={0}
                                maxSections={1}
                            />
                        </span>
                    </div>
                </>
            )
    }
    render() {
        return (
            <div className="resume13Root">
                <div className="resume13Main">
                    <div className="resume13-resumeHeader">

                        <div className="resume13-image">
                            <label className="resume13-image-label">
                                <img className="resume13-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                <input className="resume13-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                            </label>
                        </div>
                        <div className="resume13-name-position">
                            <span className="resume13-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <input
                                name="position"
                                className="resume13-positionBox"
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />
                            {/*Contact Input*/}
                            <div className="resume13-contact">
                                <div className="resume13-contact-inputs">
                                    <img src={mail} className="resume13-contact-img" alt=" " />
                                    <input
                                        value={this.props.inputVal.email}
                                        type="text"
                                        maxLength={40}
                                        onChange={(e) => this.props.updateBasic("email", e)}
                                        placeholder='john@doe.com'
                                    />
                                    <img src={globe} className="resume13-contact-img" alt=" " />
                                    <input
                                        value={this.props.inputVal.url}
                                        type="text"
                                        maxLength={35}
                                        onChange={(e) => this.props.updateBasic("url", e)}
                                        placeholder='linkedin.com/in/john-doe'
                                    />
                                </div>
                                <div className="resume13-contact-inputs">
                                    <img src={telephone} className="resume13-contact-img" alt=" " />
                                    <input
                                        value={this.props.inputVal.phone}
                                        type="text"
                                        maxLength={15}
                                        onChange={(e) => this.props.updateBasic("phone", e)}
                                        placeholder='+9199876543210'
                                    />
                                    <img src={home} className="resume13-contact-img" alt=" " />
                                    <TextareaAutosize
                                        className="resume13-address"
                                        rowsMin={1}
                                        rowsMax={4}
                                        maxLength={70}
                                        value={this.props.inputVal.address}
                                        type="text"
                                        onChange={(e) => this.props.updateBasic("address", e)}
                                        placeholder="481 Baker Street, CA"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="resume13-section-2">
                        <div className="resume13-section">
                            <div className="resume13-section-title">
                                PROFILE
                            </div>
                            <div className="resume13-input-section">
                                <div className="resume13-inputs">
                                    <TextareaAutosize
                                        name="profile"
                                        className="resume13-descriptionBox"
                                        value={this.props.inputVal.description}
                                        placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                        rowsMin={5}
                                        maxLength={300}
                                        onChange={(e) => this.props.updateBasic("description", e)}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="resume13-section">
                            {/*Skill Inputs*/}
                            <div className="resume13-section-title">SKILLS</div>
                            <div className="resume13-input-section">
                                <span className="resume13-inputs">
                                    <SkillInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.skill}
                                        inputVals={this.props.inputVal.skill}
                                        maxInputs={6}
                                    />
                                </span>
                            </div>
                        </div>
                        <div className="resume13-section">
                            <div className="resume13-section-title">HIGHEST EDUCATION</div>
                            <div className="resume13-input-section">
                                <span className="resume13-inputs">
                                    <EducationInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        numInputs={this.props.numInput.education}
                                        inputVals={this.props.inputVal.education}
                                        textCol="resume13-text-color"
                                    />
                                </span>
                            </div>
                        </div>

                        <div className="resume13-section">
                            {this.renderExp()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}