import React, { Component } from 'react';
import Fade from "react-reveal/Fade";
import Carousel from 'react-multi-carousel';
import { Card } from 'react-bootstrap';
import 'react-multi-carousel/lib/styles.css';
import './Testimonial.css';
import testimonialData from '../data/Testimonial_data';

export default class Testimonial extends Component {
  /*
    Creates Testimonial component using a react-multi-carousel.
    No props as input.
    Data for Carousel : testimonialData (import)
  */
  render() {
    return (
      <div className="TestimonialSection">
        <div className="testimonial-heading">
          <Fade top distance={"10px"}>
            <h1 className="testimonial-heading-h1">TESTIMONIALS</h1>
          </Fade>
        </div>
        <Carousel
          containerClass="carousel-container-testimonial"
          ssr={true}
          itemClass="image-item-testimonial"
          responsive={testimonialData.responsive}
          infinite={true}
          autoPlay={true}
          centerMode={true}
          removeArrowOnDeviceType={["desktop", "tablet", "mobile"]}
          autoPlaySpeed={2000}
        >

          {testimonialData.data.map((object, index) => {
            return (
              <Card style={styles.card} key={index}>
                <Card.Img src={object.image} />
                <Card.Body style={styles.body}>
                  <Card.Title style={{ fontWeight: 'bold' }}>{object.name}</Card.Title>
                  <Card.Text className="testimonial-card-text">
                    {object.review}
                  </Card.Text>
                </Card.Body>
              </Card>
            );
          })}
        </Carousel>
      </div>
    )
  }
}

const styles = {
  card: {
    width: '80%',
    height: '400px',
    marginTop: '20%',
    marginBottom: '30%',
    backgroundColor: 'rgba(255,255,255,0)',
    color: 'black',
    border: 'none'
  },
  body: {
    textAlign: 'center',
    padding: '1rem 0.5rem'
  }
}