import React from 'react'
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import { Button } from '@material-ui/core';
import CustomTextarea from '../customTextarea';
import ComplexSectionComponent from '../complexSectionComponent';

export default class CertificationInput extends ComplexSectionComponent {

    renderInputs = () => {
        let certificationInputs = [];
        for (let i = 0; i < this.state.numInputs; i++) {
            certificationInputs.push(
                <div key={i} style={styles.inputs}>
                    <input
                        value={this.state.values[i].name}
                        onChange={(e) => this.updateValue('certificate', e, 'name', i)}
                        placeholder="Certificate Name"
                    />
                    <input
                        value={this.state.values[i].certifying_center}
                        onChange={(e) => this.updateValue('certificate', e, 'certifying_center', i)}
                        placeholder="Certifying Centre"
                    />
                    <CustomTextarea
                        updateTextarea={(description) => this.updateTextarea('certificate', description, 'description', i)}
                        placeholder={placeholders}
                        values={this.state.values[i].description}
                        ulclass={this.props.ulClass}
                    />
                    Date of Obatainment:<input
                        maxLength='10'
                        placeholder="2012-01-01"
                        value={this.state.values[i].obtainment_date}
                        onChange={(e) => this.updateValue('certificate', e, 'obtainment_date', i)}
                    />
                </div>
            );
        }
        return certificationInputs;
    }

    render() {
        return (
            <>
                {this.renderInputs()}
                <div style={styles.btns}>
                    <Button onClick={() => this.incrementInputs('certificate')}><AddIcon fontSize="small" /></Button>
                    <Button onClick={() => this.decrementInputs('certificate')}><RemoveIcon fontSize="small" /></Button>
                </div>
            </>
        )
    }
}

let styles = {
    btns: {
        display: 'flex'
    },
    inputs: {
        display: 'flex',
        flexDirection: 'column',
    }
}

let placeholders = {

}