import React, { Component } from 'react';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import ProjectInput from '../components/complexSections/ProjectInput';
import CertificationInput from '../components/complexSections/CertificationInput';
import RecommendationInput from '../components/simpleSections/RecommendationInput';
import './resume11.css';

export default class Resume11P2 extends Component {

    renderExp = () => {
        return (
            <div>
                <ExperienceInput
                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                    updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                    numInputs={this.props.numInput.experience}
                    inputVals={this.props.inputVal.experience}
                    ulClass="resume11-experience-ul"
                    textCol="resume11-text-color"
                    startNum={2}
                    maxSections={5}
                />
            </div>
        )
    }

    renderProject = () => {
        if (this.props.numInput.projects)
            return (<>
                <p className="resume11-section-title">PROJECTS</p>
                <span className="resume11-inputs-left">
                    <ProjectInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.project}
                        inputVals={this.props.inputVal.project}
                        ulClass="resume11-project-ul"
                        maxSections={2}
                    />
                </span>
            </>
            )
        else
            return (null)
    }

    renderCertificate = () => {
        if (this.props.numInput.certificates)
            return (<>
                <div className='resume11-section'>
                    <p className="resume11-section-title">CERTIFICATES</p>
                    <span className="resume11-inputs-left">
                        <CertificationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.certificate}
                            inputVals={this.props.inputVal.certificate}
                            ulClass="resume11-certificate-ul"
                            maxSections={2}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    renderRecommendation = () => {
        if (this.props.numInput.recommendations)
            return (<>
                <div className='resume11-section'>
                    <p className="resume11-section-title">PERSONAL REFERENCES</p>
                    <span className="resume11-inputs-left">
                        <RecommendationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.recommendation}
                            inputVals={this.props.inputVal.recommendation}
                            recommendationclass="resume11-recommendation-list"
                            maxInputs={3}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    render() {
        return (
            <>
                <div ref={this.props.refProp} className="resume11P2Root">
                    <div className="resume11Main">

                        <div className="resume11-section-2-P2">
                            <div className="resume11-left-section">
                                {this.renderProject()}

                                {this.renderCertificate()}

                                {this.renderRecommendation()}

                            </div>
                            <div className="resume11-right-section">

                                {this.renderExp()}

                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}