import React, { Component } from 'react';
import { Tab, Tabs } from 'react-bootstrap'
import Login from './modules/LoginPage/Login'
import SignUp from './modules/SignUpPage/SignUp'
import { Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import './RegisterPage.css';

class RegisterPage extends Component {

    render() {
        if (this.props.isAuthenticated) {
            if (localStorage.getItem('route') !== null) {
                setTimeout(() => {
                    const toPath = localStorage.getItem('route')
                    localStorage.removeItem('route')
                    return window.location.href = (toPath);
                }, 300);
            } else {
                return <Redirect to="/" />
            }
        }
        return (
            <div className="register-main">
                {this.props.loading ?
                    <div className="register-loader"><CircularProgress
                        style={{ 'color': '#fede00' }} /></div> :
                    null
                }
                <div className="register-box">
                    <Tabs defaultActiveKey="sign-up-tab" variant="head-tabs">
                        <Tab eventKey="login-tab" title="Login">
                            <Login />
                        </Tab>
                        <Tab eventKey="sign-up-tab" title="Sign Up">
                            <SignUp />
                        </Tab>
                    </Tabs>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.token !== null,
        loading: state.loading,
    }
}

export default connect(mapStateToProps, null)(RegisterPage);