import React, { Component } from 'react';
import AchievementInput from '../components/simpleSections/AchievementInput';
import SkillInput from '../components/simpleSections/SkillInput';
import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resume15.css';

export default class Resume15P1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <div>
                    <p className="resume15-section-title">INERNSHIP / COURSE</p>
                    <span className="resume15-inputs">
                        <InternInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            numInputs={this.props.numInput.intern}
                            inputVals={this.props.inputVal.intern}
                            textCol="resume15-text-color"
                        />
                    </span>
                </div>
            )
        else
            return (
                <div>
                    <p className="resume15-section-title">WORK EXPERIENCE</p>
                    <span className="resume15-inputs">
                        <ExperienceInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.experience}
                            inputVals={this.props.inputVal.experience}
                            ulClass="resume15-experience-ul"
                            textCol="resume15-text-color"
                            startNum={0}
                            maxSections={2}
                        />
                    </span>
                </div>
            )
    }
    render() {
        return (
            <div className="resume15Root">
                <div className="resume15Main">
                    <div className="resume15-resumeHeader">

                        <div className="resume15-Image">
                            <label className="resume15-image-label">
                                <img className="resume15-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                <input className="resume15-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                            </label>
                        </div>
                        <div className="resume15-name-position">
                            <span className="resume15-capBox resume15-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <input
                                name="position"
                                maxLength={60}
                                className="resume15-capBox resume15-positionBox "
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />
                            <div className="resume15-contact-section">
                                <div className="resume15-contact-address">
                                    <span className="resume15-contact-title">ADDRESS</span>
                                    <TextareaAutosize
                                        className="resume15-address"
                                        rowsMin={1}
                                        rowsMax={3}
                                        maxLength={110}
                                        value={this.props.inputVal.address}
                                        type="text"
                                        onChange={(e) => this.props.updateBasic("address", e)}
                                        placeholder="481 Baker Street, CA"
                                    />
                                </div>
                                <div className="resume15-contact-phone">
                                    <span className="resume15-contact-title">PHONE</span>
                                    <input
                                        value={this.props.inputVal.phone}
                                        type="text"
                                        maxLength={15}
                                        onChange={(e) => this.props.updateBasic("phone", e)}
                                        placeholder='+9199876543210'
                                    />
                                </div>
                                <div className="resume15-contact-website">
                                    <span className="resume15-contact-title">EMAIL</span>
                                    <input
                                        value={this.props.inputVal.email}
                                        type="text"
                                        onChange={(e) => this.props.updateBasic("email", e)}
                                        placeholder='johndoe@gmail.com'
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="resume15-profile">
                        <p className="resume15-section-title">PROFILE</p>
                        <TextareaAutosize
                            name="profile"
                            className="resume15-descriptionBox"
                            value={this.props.inputVal.description}
                            placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                            rowsMin={3}
                            maxLength={300}
                            onChange={(e) => this.props.updateBasic("description", e)}
                        />
                    </div>

                    <div className="resume15-section-2">
                        <div className="resume15-left-section">

                            {/*Skill Inputs*/}
                            <p className="resume15-section-title">SKILLS</p>
                            <span className="resume15-inputs">
                                <SkillInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                    numInputs={this.props.numInput.skill}
                                    inputVals={this.props.inputVal.skill}
                                    maxInputs={6}
                                />
                            </span>

                        </div>
                        <div className="resume15-right-section">

                            {/* Interest Inputs */}
                            <p className="resume15-section-title">HOBBIES</p>
                            <span className="resume15-inputs">
                                <InterestInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                    numInputs={this.props.numInput.interest}
                                    inputVals={this.props.inputVal.interest}
                                    maxInputs={4}
                                />
                            </span>

                        </div>
                    </div>
                    <div className="resume15-section-3">
                        <div className="resume15-left-section">

                            {this.renderExp()}

                        </div>

                        <div className="resume15-right-section">

                            {/*Achievement Inputs*/}
                            <p className="resume15-section-title">ACHIEVEMENTS</p>
                            <span className="resume15-inputs">
                                <AchievementInput
                                    updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                    updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                    numInputs={this.props.numInput.achievement}
                                    inputVals={this.props.inputVal.achievement}
                                    maxInputs={4}
                                />
                            </span>

                            <div className="resume15-section">
                                <p className="resume15-section-title">HIGHEST EDUCATION</p>
                                <span className="resume15-inputs">
                                    <EducationInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        numInputs={this.props.numInput.education}
                                        inputVals={this.props.inputVal.education}
                                        textCol="resume15-text-color"
                                    />
                                </span>
                            </div>

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}