import React, { Component } from 'react';
import AchievementInput from '../components/simpleSections/AchievementInput';
import SkillInput from '../components/simpleSections/SkillInput';
import ContactInput from '../components/simpleSections/ContactInput';
import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resume14.css';

export default class Resume14P1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <div className='resume14-section'>
                    <p className="resume14-section-title">Internship / Course</p>
                    <span className="resume14-inputs">
                        <InternInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            numInputs={this.props.numInput.intern}
                            inputVals={this.props.inputVal.intern}
                            textCol="resume14-text-color"
                        />
                    </span>
                </div>
            )
        else
            return (
                <div className='resume14-section'>
                    <p className="resume14-section-title">Work Experience</p>
                    <span className="resume14-inputs">
                        <ExperienceInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.experience}
                            inputVals={this.props.inputVal.experience}
                            ulClass="resume14-experience-ul"
                            textCol="resume14-text-color"
                            startNum={0}
                            maxSections={2}
                        />
                    </span>
                </div>
            )
    }
    render() {
        return (
            <div className="resume14Root">
                <div className="resume14Main">
                    <div className="resume14-resumeHeader">
                        <span className="resume14-capBox resume14-nameBox">
                            {this.props.inputVal.name}
                        </span>
                        <input
                            name="position"
                            maxLength={60}
                            className="resume14-capBox resume14-positionBox "
                            placeholder="Fresher"
                            value={this.props.inputVal.position}
                            onChange={(e) => this.props.updateBasic("position", e)}
                        />
                    </div>

                    <div className="resume14-section-2">
                        <div className="resume14-left-section">

                            <div className="resume14-section">
                                <p className="resume14-section-title">Profile</p>
                                <TextareaAutosize
                                    name="profile"
                                    className="resume14-descriptionBox"
                                    value={this.props.inputVal.description}
                                    placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                    rowsMin={6}
                                    maxLength={380}
                                    onChange={(e) => this.props.updateBasic("description", e)}
                                />
                            </div>

                            {/*Contact Inputs*/}
                            <div className="resume14-section">
                                <p className="resume14-section-title">Contact Details</p>
                                <span className="resume14-inputs">
                                    <ContactInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        inputVals={this.props.inputVal}
                                    />
                                </span>
                            </div>

                            {/* Skill Inputs */}
                            <div className="resume14-section">
                                <p className="resume14-section-title">Skills</p>
                                <span className="resume14-inputs">
                                    <SkillInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.skill}
                                        inputVals={this.props.inputVal.skill}
                                        maxInputs={6}
                                    />
                                </span>
                            </div>

                            {/* Interest Inputs */}
                            <div className="resume14-section">
                                <p className="resume14-section-title">Interest</p>
                                <span className="resume14-inputs">
                                    <InterestInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.interest}
                                        inputVals={this.props.inputVal.interest}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>

                            {/*Achievement Inputs*/}
                            <div className="resume14-section">
                                <p className="resume14-section-title">Achievements</p>
                                <span className="resume14-inputs">
                                    <AchievementInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.achievement}
                                        inputVals={this.props.inputVal.achievement}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>

                        </div>
                        <div className="resume14-right-section">

                            <div className="resume14-section">
                                <p className="resume14-section-title">Highest Education</p>
                                <span className="resume14-inputs">
                                    <EducationInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        numInputs={this.props.numInput.education}
                                        inputVals={this.props.inputVal.education}
                                        textCol="resume14-text-color"
                                    />
                                </span>
                            </div>

                            {this.renderExp()}

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}