import React, { Component } from 'react'
import Fade from "react-reveal/Fade";
import Carousel from 'react-multi-carousel';
import { Image } from 'react-bootstrap';
import 'react-multi-carousel/lib/styles.css';
import './GrowthStories.css';
import growthData from '../data/Growth_data';

export default class GrowthStories extends Component {

  render() {
    return (
      <div className="growthSection">
        <div className="growthSection-heading">
          <Fade bottom distance={"5px"}>
            <h1 className="growth-stories-h1">GROWTH STORIES</h1>
          </Fade>

          <Fade bottom delay={500} distance={"5px"}>
            <span className="growth-stories-span">Boosted resumes helped aspirants get placed in top companies.</span>
          </Fade>
        </div>
        <Carousel
          containerClass="carousel-container"
          ssr={true}
          itemClass="image-item"
          responsive={growthData.responsive}
          infinite={true}
          autoPlay={true}
          removeArrowOnDeviceType={["desktop", "tablet", "mobile"]}
          autoPlaySpeed={2000}
        >

          {growthData.data.slice(0, 10).map((object, index) => {
            return (
              <Image className="image-logo"
                draggable={false}
                src={object}
                key={index}
              />
            );
          })}
        </Carousel>
      </div>
    )
  }
}