import axios from 'axios';

const submit = (props) => {
    let value = props.inputVal
    let num = props.numInput
    let page2active = String(props.page2active)
    let internActive = String(num.intern)
    let projectActive = String(num.projects)
    let certificateActive = String(num.certificates)
    let recommendationActive = String(num.recommendations)
    let gpa = parseFloat(value.education.gpa)
    axios.post('resume/api/resume/',
        {
            type: 'pdf',
            page2active: page2active,
            internActive: internActive,
            projectActive: projectActive,
            certificateActive: certificateActive,
            recommendationActive: recommendationActive,
            achievementNum: num.achievement,
            skillNum: num.skill,
            interestNum: num.interest,
            experienceNum: num.experience,
            projectNum: num.project,
            certificateNum: num.certificate,
            recommendationNum: num.recommendation,
            user: value.user,
            resume_number: value.resume_number,
            user_resume_image: value.resume_image,
            name: value.name,
            age: value.age,
            position: value.position,
            profile: value.description,
            email: value.email,
            phone: value.phone,
            address: value.address,
            url: value.url,
            achievements: value.achievement[0]
                + ',,' + value.achievement[1] +
                ',,' + value.achievement[2] +
                ',,' + value.achievement[3] +
                ',,' + value.achievement[4],
            skills: value.skill[0]
                + ',,' + value.skill[1] +
                ',,' + value.skill[2] +
                ',,' + value.skill[3] +
                ',,' + value.skill[4],
            interests: value.interest[0]
                + ',,' + value.interest[1] +
                ',,' + value.interest[2] +
                ',,' + value.interest[3] +
                ',,' + value.interest[4],
            education_institute: value.education.institute,
            education_degree: value.education.degree,
            education_course: value.education.course,
            education_start: value.education.start_date,
            education_end: value.education.end_date,
            education_score: gpa,
            intern_company: value.intern.company,
            intern_position: value.intern.position,
            intern_start: value.intern.start_date,
            intern_end: value.intern.end_date,
            intern_description: value.intern.description[0]
                + ',,' + value.intern.description[1] +
                ',,' + value.intern.description[2] +
                ',,' + value.intern.description[3] +
                ',,' + value.intern.description[4] +
                ',,' + value.intern.description[5],
            experience_company_1: value.experience[0].company,
            experience_position_1: value.experience[0].position,
            experience_start_1: value.experience[0].start_date,
            experience_end_1: value.experience[0].end_date,
            experience_description_1: value.experience[0].description[0]
                + ',,' + value.experience[0].description[1] +
                ',,' + value.experience[0].description[2] +
                ',,' + value.experience[0].description[3] +
                ',,' + value.experience[0].description[4] +
                ',,' + value.experience[0].description[5],
            experience_company_2: value.experience[1].company,
            experience_position_2: value.experience[1].position,
            experience_start_2: value.experience[1].start_date,
            experience_end_2: value.experience[1].end_date,
            experience_description_2: value.experience[1].description[0]
                + ',,' + value.experience[1].description[1] +
                ',,' + value.experience[1].description[2] +
                ',,' + value.experience[1].description[3] +
                ',,' + value.experience[1].description[4] +
                ',,' + value.experience[1].description[5],
            experience_company_3: value.experience[2].company,
            experience_position_3: value.experience[2].position,
            experience_start_3: value.experience[2].start_date,
            experience_end_3: value.experience[2].end_date,
            experience_description_3: value.experience[2].description[0]
                + ',,' + value.experience[2].description[1] +
                ',,' + value.experience[2].description[2] +
                ',,' + value.experience[2].description[3] +
                ',,' + value.experience[2].description[4] +
                ',,' + value.experience[2].description[5],
            experience_company_4: value.experience[3].company,
            experience_position_4: value.experience[3].position,
            experience_start_4: value.experience[3].start_date,
            experience_end_4: value.experience[3].end_date,
            experience_description_4: value.experience[3].description[0]
                + ',,' + value.experience[3].description[1] +
                ',,' + value.experience[3].description[2] +
                ',,' + value.experience[3].description[3] +
                ',,' + value.experience[3].description[4] +
                ',,' + value.experience[3].description[5],
            experience_company_5: value.experience[4].company,
            experience_position_5: value.experience[4].position,
            experience_start_5: value.experience[4].start_date,
            experience_end_5: value.experience[4].end_date,
            experience_description_5: value.experience[4].description[0]
                + ',,' + value.experience[4].description[1] +
                ',,' + value.experience[4].description[2] +
                ',,' + value.experience[4].description[3] +
                ',,' + value.experience[4].description[4] +
                ',,' + value.experience[4].description[5],
            project_name_1: value.project[0].name,
            project_start_1: value.project[0].start_date,
            project_end_1: value.project[0].end_date,
            project_description_1: value.project[0].description[0]
                + ',,' + value.project[0].description[1] +
                ',,' + value.project[0].description[2] +
                ',,' + value.project[0].description[3] +
                ',,' + value.project[0].description[4] +
                ',,' + value.project[0].description[5],
            project_name_2: value.project[1].name,
            project_start_2: value.project[1].start_date,
            project_end_2: value.project[1].end_date,
            project_description_2: value.project[1].description[0]
                + ',,' + value.project[1].description[1] +
                ',,' + value.project[1].description[2] +
                ',,' + value.project[1].description[3] +
                ',,' + value.project[1].description[4] +
                ',,' + value.project[1].description[5],
            project_name_3: value.project[2].name,
            project_start_3: value.project[2].start_date,
            project_end_3: value.project[2].end_date,
            project_description_3: value.project[2].description[0]
                + ',,' + value.project[2].description[1] +
                ',,' + value.project[2].description[2] +
                ',,' + value.project[2].description[3] +
                ',,' + value.project[2].description[4] +
                ',,' + value.project[2].description[5],
            certificate_name_1: value.certificate[0].name,
            certifying_center_1: value.certificate[0].certifying_center,
            certificate_description_1: value.certificate[0].description[0]
                + ',,' + value.certificate[0].description[1] +
                ',,' + value.certificate[0].description[2] +
                ',,' + value.certificate[0].description[3] +
                ',,' + value.certificate[0].description[4] +
                ',,' + value.certificate[0].description[5],
            certificate_obtainment_date_1: value.certificate[0].obtainment_date,
            certificate_name_2: value.certificate[1].name,
            certifying_center_2: value.certificate[1].certifying_center,
            certificate_description_2: value.certificate[1].description[0]
                + ',,' + value.certificate[1].description[1] +
                ',,' + value.certificate[1].description[2] +
                ',,' + value.certificate[1].description[3] +
                ',,' + value.certificate[1].description[4] +
                ',,' + value.certificate[1].description[5],
            certificate_obtainment_date_2: value.certificate[1].obtainment_date,
            certificate_name_3: value.certificate[2].name,
            certifying_center_3: value.certificate[2].certifying_center,
            certificate_description_3: value.certificate[2].description[0]
                + ',,' + value.certificate[2].description[1] +
                ',,' + value.certificate[2].description[2] +
                ',,' + value.certificate[2].description[3] +
                ',,' + value.certificate[2].description[4] +
                ',,' + value.certificate[2].description[5],
            certificate_obtainment_date_3: value.certificate[2].obtainment_date,
            recommendations: value.recommendation[0]
                + ',,' + value.recommendation[1]
                + ',,' + value.recommendation[2]
                + ',,' + value.recommendation[3]
                + ',,' + value.recommendation[4],
        }
    ).then(res => {
        let name = value.name
        const url = res.data.directory
        axios({
            url: url,
            method: 'GET',
            responseType: 'blob',
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', name + '.pdf');
            document.body.appendChild(link);
            link.click();
        });
        // if (res.status === 200)
        //     window.open(res.data.directory);

        // const url = window.URL.createObjectURL(res.data.directory);
        // const link = document.createElement('a');
        // link.href = url
        // link.setAttribute('download', 'file.pdf');
        // document.body.appendChild(link);
        // link.click();

        // const filePath = res.data.directory
        // const link = document.createElement('a');
        // link.href = filePath;
        // link.download = filePath.substr(filePath.lastIndexOf('/') + 1);
        // link.click();

        pngSubmit(props)

    }).catch(err => {
        console.log(err)
        return "Network Error"
    })
}

