import React from 'react';
import CustomTextarea from '../customTextarea';
import SingleSectionComponent from '../singleSectionComponent';

export default class InternInput extends SingleSectionComponent {

  renderInputs = () => {
    let internInputs = [];
    internInputs.push(
      <div key={0} style={styles.inputs}>
        <input
          style={styles.position}
          value={this.state.values.position}
          onChange={(e) => this.updateValue('intern', e, 'position')}
          placeholder="Marketing Assistant"
        />
        <input
          value={this.state.values.company}
          className={this.props.textCol}
          onChange={(e) => this.updateValue('intern', e, 'company')}
          placeholder="Immersive Studios"
        />
        <div style={styles.dateInputs}>
          <input
            style={styles.dateInput}
            maxLength='10'
            value={this.state.values.start_date}
            onChange={(e) => this.updateValue('intern', e, 'start_date')}
            placeholder="2012"
          />
          <input
            style={styles.dateInput}
            maxLength='10'
            value={this.state.values.end_date}
            onChange={(e) => this.updateValue('intern', e, 'end_date')}
            placeholder="2016"
          />
        </div>
        <CustomTextarea
          updateTextarea={(description) => this.updateTextarea('intern', description, 'description')}
          placeholder={placeholders}
          values={this.state.values.description}
          ulclass={this.props.ulClass}
        />
      </div>
    );

    return internInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
      </>
    )
  }
}

let styles = {
  inputs: {
    display: 'flex',
    flexDirection: 'column',
  },
  position: {
    fontSize: '15px',
    fontWeight: '500',
  },
  dateInputs: {
    display: 'flex'
  },
  dateInput: {
    width: '100px',
    marginRight: '10px'
  }
}

let placeholders = [
  'Handled the company’s online presence – regularly updated the company’s website and various social media accounts',
  'Maintained and organized numerous office files.',
  'Constantly updated the company’s contact and mailing lists.',
  'Monitored ongoing marketing campaigns.',
  'Prepared presentations for prospective clients',
  'Monitored press coverage'
]