import { Component } from 'react'

class ComplexSectionComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            numInputs: props.numInputs,
            values: props.inputVals
        }
    }

    updateValue = (sectionName, e, key, i) => {
        let inputValue = e.target.value;
        let currData = this.state.values;
        currData[i][key] = inputValue;

        this.setState({
            values: currData
        }, () => {
            this.props.updateSection(sectionName, this.state.values);
        });
    }

    updateTextarea = (sectionName, e, key, i) => {
        let inputValue = e;
        let currData = this.state.values;
        currData[i][key] = inputValue;

        this.setState({
            values: currData
        }, () => {
            this.props.updateSection(sectionName, this.state.values);
        });
    }

    incrementInputs = (sectionName) => {
        if (this.state.numInputs < this.props.maxSections) {
            this.setState({
                numInputs: this.state.numInputs + 1
            }, () => {
                this.props.updateNuminputs(sectionName, this.state.numInputs);
            });
        }
    }

    decrementInputs = (sectionName) => {
        if (this.state.numInputs > 1) {
            this.setState({
                numInputs: this.state.numInputs - 1
            }, () => {
                this.props.updateNuminputs(sectionName, this.state.numInputs);
            });
        }
    }
}

export default ComplexSectionComponent;
