import React from 'react'
import SimpleSectionComponent from '../simpleSectionComponent';
//import TextareaAutosize from '@material-ui/core/TextareaAutosize';


export default class SkillInput extends SimpleSectionComponent {

  renderInputs = () => {
    let skillInputs = [];

    for (let i = 1; i <= this.state.numInputs; i++) {
      skillInputs.push(
        <input
          style={styles.input}
          ref={i}
          key={i}
          value={this.state.values[i - 1]}
          onChange={(e) => this.updateValue('skill', e, i - 1)}
          placeholder={placeholders[i - 1]}
          onKeyDown={(e) => this.handleKeyDown('skill', i, e, i + 1)}
        />
      );
    }
    return skillInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
      </>
    )
  }
}

let styles = {
  input: {
    resize: 'none'
  }
}

let placeholders = [
  'Exceptional communication',
  'Networking skills',
  'Organizational Skills',
  'Critical Thinking Skills',
  'Leadership Skills'
]