import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import './ProfileThumbs.css'

class ProfileThumbs extends Component {
    constructor(props) {
        super(props)
        this.state = {
            paths: []
        }
    }

    componentWillMount() {
        axios.get('http://127.0.0.1:8000/resume/api/resume-png/?user=' + this.props.user
        ).then(res => {
            this.setState({
                paths: res.data.data
            })
        }).catch(err => {
            console.log(err)
        })
    }

    renderThumbs = () => {
        let data = []
        for (let i = 0; i < this.state.paths.length; i++)
            data.push(
                <div key={this.state.paths[i].id} className="profile-thumbnail-img">
                    <Link to={'resume-edit-' + this.state.paths[i].resume_number}>
                        <img
                            src={this.state.paths[i].path}
                            //  + "?" + (new Date()).getTime()}
                            alt="thumbnail"
                            width="240px"
                        />
                    </Link>
                </div>
            )
        return data;
    }

    render() {
        const path = this.state.paths
        return (
            <div className="profile-thumbnails-main">
                <div className="profile-title">
                    MY RESUMES
                </div>
                <div className="profile-thumbnails">
                    {path.length ? this.renderThumbs() : 'No Resumes Found'}
                </div>
            </div>
        );
    }
}

export default ProfileThumbs;