import React, { Component } from 'react';
import AchievementInput from '../components/simpleSections/AchievementInput';
import SkillInput from '../components/simpleSections/SkillInput';
import ContactInput from '../components/simpleSections/ContactInput';
// import InterestInput from '../components/simpleSections/InterestInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resumeFour.css';

export default class ResumeFourP1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <>
                    <div className="resumeFour-section-title">INTERNSHIP / COURSE</div>
                    <div className="resumeFour-input-section">
                        <span className="resumeFour-inputs">
                            <InternInput
                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                numInputs={this.props.numInput.intern}
                                inputVals={this.props.inputVal.intern}
                                textCol="resumeFour-text-color"
                            />
                        </span>
                    </div>
                </>
            )
        else
            return (
                <>
                    <div className="resumeFour-section-title">WORK EXPERIENCE</div>
                    <div className="resumeFour-input-section">
                        <span className="resumeFour-inputs">
                            <ExperienceInput
                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                numInputs={this.props.numInput.experience}
                                inputVals={this.props.inputVal.experience}
                                ulClass="resumeFour-experience-ul"
                                textCol="resumeFour-text-color"
                                startNum={0}
                                maxSections={1}
                            />
                        </span>
                    </div>
                </>
            )
    }
    render() {
        return (
            <div className="resumeFourRoot">
                <div className="resumeFourMain">
                    <div className="resumeFour-resumeHeader">

                        <div className="resumeFour-image">
                            <label className="resumeFour-image-label">
                                <img className="resumeFour-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                <input className="resumeFour-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                            </label>
                        </div>
                        <div className="resumeFour-name-position">
                            <span className="resumeFour-capBox resumeFour-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <input
                                name="position"
                                className="resumeFour-capBox resumeFour-positionBox"
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />
                        </div>
                        {/*Contact Input*/}
                        <div className="resumeFour-contact-inputs">
                            <ContactInput
                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                inputVals={this.props.inputVal}
                            />
                        </div>
                    </div>

                    <div className="resumeFour-section-2">
                        <div className="resumeFour-section">
                            <div className="resumeFour-section-title">
                                PROFILE
                            </div>
                            <div className="resumeFour-input-section">
                                <div className="resumeFour-inputs">
                                    <TextareaAutosize
                                        name="profile"
                                        className="resumeFour-descriptionBox"
                                        value={this.props.inputVal.description}
                                        placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                        rowsMin={5}
                                        maxLength={300}
                                        onChange={(e) => this.props.updateBasic("description", e)}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="resumeFour-section">
                            {/*Skill Inputs*/}
                            <div className="resumeFour-section-title">SKILLS</div>
                            <div className="resumeFour-input-section">
                                <span className="resumeFour-inputs">
                                    <SkillInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.skill}
                                        inputVals={this.props.inputVal.skill}
                                        maxInputs={6}
                                    />
                                </span>
                            </div>
                        </div>

                        <div className="resumeFour-section">
                            {/*Achievement Inputs*/}
                            <div className="resumeFour-section-title">ACHIEVEMENTS</div>
                            <div className="resumeFour-input-section">
                                <span className="resumeFour-inputs">
                                    <AchievementInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                        numInputs={this.props.numInput.achievement}
                                        inputVals={this.props.inputVal.achievement}
                                        maxInputs={4}
                                    />
                                </span>
                            </div>
                        </div>

                        <div className="resumeFour-section">
                            <div className="resumeFour-section-title">EDUCATION</div>
                            <div className="resumeFour-input-section">
                                <span className="resumeFour-inputs">
                                    <EducationInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        numInputs={this.props.numInput.education}
                                        inputVals={this.props.inputVal.education}
                                        textCol="resumeFour-text-color"
                                    />
                                </span>
                            </div>
                        </div>

                        <div className="resumeFour-section">
                            {this.renderExp()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}