import React, { Component } from 'react';
import InterestInput from '../components/simpleSections/InterestInput';
import ContactInput from '../components/simpleSections/ContactInput';
import SkillInput from '../components/simpleSections/SkillInput';
import InternInput from '../components/singleSections/InternInput';
import EducationInput from '../components/singleSections/EducationInput';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import TextareaAutosize from '@material-ui/core/TextareaAutosize';
import './resumeTen.css';

export default class ResumeTenP1 extends Component {

    renderExp = () => {
        if (this.props.numInput.intern === true)
            return (
                <div className='resumeTen-section'>
                    <p className="resumeTen-section-title">INTERNSHIP / COURSE</p>
                    <span className="resumeTen-inputs">
                        <InternInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            numInputs={this.props.numInput.intern}
                            inputVals={this.props.inputVal.intern}
                            textCol="resumeTen-text-color"
                        />
                    </span>
                </div>
            )
        else
            return (
                <div className='resumeTen-section'>
                    <p className="resumeTen-section-title">WORK HISTORY</p>
                    <span className="resumeTen-inputs">
                        <ExperienceInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.experience}
                            inputVals={this.props.inputVal.experience}
                            ulClass="resumeTen-experience-ul"
                            textCol="resumeTen-text-color"
                            startNum={0}
                            maxSections={2}
                        />
                    </span>
                </div>
            )
    }
    render() {
        return (
            <div className="resumeTenRoot">
                <div className="resumeTenMain">

                    <div className="resumeTen-section-2">
                        <div className="resumeTen-left-section">
                            <div className="resumeTen-image">
                                <label className="resumeTen-image-label">
                                    <img className="resumeTen-Imageholder" src={this.props.inputVal.resume_image} alt="" />
                                    <input className="resumeTen-image-upload-btn" type="file" onChange={(e) => this.props.updateImage(e)} />
                                </label>
                            </div>
                            <span className="resumeTen-capBox resumeTen-nameBox">
                                {this.props.inputVal.name}
                            </span>
                            <TextareaAutosize
                                name="position"
                                rowsMin={4}
                                maxLength={35}
                                className="resumeTen-capBox resumeTen-positionBox "
                                placeholder="Fresher"
                                value={this.props.inputVal.position}
                                onChange={(e) => this.props.updateBasic("position", e)}
                            />

                            <div className="resumeTen-flex">
                                <div className="resumeTen-flex-items">
                                    {/*Contact Inputs*/}
                                    <div className="resumeTen-section">
                                        <p className="resumeTen-section-title">CONTACT</p>
                                        <span className="resumeTen-inputs">
                                            <ContactInput
                                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                                inputVals={this.props.inputVal}
                                            />
                                        </span>
                                    </div>

                                    {/* Skill Inputs */}
                                    <div className="resumeTen-section">
                                        <p className="resumeTen-section-title">SKILLS</p>
                                        <span className="resumeTen-inputs">
                                            <SkillInput
                                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                                updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                                numInputs={this.props.numInput.skill}
                                                inputVals={this.props.inputVal.skill}
                                                maxInputs={6}
                                            />
                                        </span>
                                    </div>

                                    {/* Achievement Inputs */}
                                    <div className="resumeTen-section">
                                        <p className="resumeTen-section-title">HOBBIES</p>
                                        <span className="resumeTen-inputs">
                                            <InterestInput
                                                updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                                updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                                                numInputs={this.props.numInput.interest}
                                                inputVals={this.props.inputVal.interest}
                                                maxInputs={4}
                                            />
                                        </span>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="resumeTen-right-section">
                            <p className="resumeTen-section-title">PROFILE</p>
                            <TextareaAutosize
                                name="profile"
                                className="resumeTen-descriptionBox"
                                value={this.props.inputVal.description}
                                placeholder="Extremely motivated to constantly develop my skills and grow professionally. I am confident in my ability to come up with interesting ideas for unforgettable marketing campaigns."
                                rowsMin={8}
                                rowsMax={8}
                                maxLength={400}
                                onChange={(e) => this.props.updateBasic("description", e)}
                            />

                            <div className="resumeTen-section">
                                <p className="resumeTen-section-title">EDUCATION</p>
                                <span className="resumeTen-inputs">
                                    <EducationInput
                                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                                        numInputs={this.props.numInput.education}
                                        inputVals={this.props.inputVal.education}
                                        textCol="resumeTen-text-color"
                                    />
                                </span>
                            </div>

                            {this.renderExp()}

                        </div>
                    </div>
                </div>
            </div >
        )
    }
}