import React, { Component } from 'react'
import Flip from "react-reveal/Flip";
import { Image } from 'react-bootstrap';
import ResumeButtons from '../resumeButtons/resumeButtons';
import './resumeBox.css';

export default class ResumeBox extends Component {
    /*
        Generates a resume's component. 
        Input props (from parent):
            resumeImage : Image of resume which is to be shown in component.
            ViewLink    : View Link to attach to view button
            Edit Link   : Edit Link to attach to edit button 
    */
    render() {
        return (
            <div className="resume-div">
                <Flip top delay={500} duration={800}>
                    <div className="resume-inner">
                        <Image src={this.props.resumeImage} className="resume-template" />
                        <ResumeButtons
                            ViewLink={this.props.ViewLink}
                            EditLink={this.props.EditLink}
                        />
                    </div>
                </Flip>
            </div>
        )
    }
}
