import React, { Component } from 'react';
import ExperienceInput from '../components/complexSections/ExperienceInput';
import ProjectInput from '../components/complexSections/ProjectInput';
import CertificationInput from '../components/complexSections/CertificationInput';
import RecommendationInput from '../components/simpleSections/RecommendationInput';
import './resume15.css';

export default class Resume15P2 extends Component {
    renderExp = () => {
        return (
            <div>
                <div className="resume15-section-title">{" "}</div>
                <span className="resume15-experience-inputs">
                    <ExperienceInput
                        updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                        updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                        numInputs={this.props.numInput.experience}
                        inputVals={this.props.inputVal.experience}
                        ulClass="resume15-experience-ul"
                        textCol="resume15-text-color"
                        startNum={2}
                        maxSections={4}
                    />
                </span>
            </div>
        )
    }

    renderProject = () => {
        if (this.props.numInput.projects)
            return (<>
                <div className="resume15-section-title">PROJECT</div>
                <div className="resume15-input-section">
                    <span className="resume15-inputs">
                        <ProjectInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.project}
                            inputVals={this.props.inputVal.project}
                            ulClass="resume15-project-ul"
                            maxSections={2}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    renderCertificate = () => {
        if (this.props.numInput.certificates)
            return (<>
                <div className="resume15-section-title">CERTIFICATE</div>
                <div className="resume15-input-section">
                    <span className="resume15-inputs">
                        <CertificationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.certificate}
                            inputVals={this.props.inputVal.certificate}
                            ulClass="resume15-certificate-ul"
                            maxSections={2}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }

    renderRecommendation = () => {
        if (this.props.numInput.recommendations)
            return (<>
                <div className="resume15-section-title">PERSONAL REFERENCES</div>
                <div className="resume15-input-section">
                    <span className="resume15-inputs">
                        <RecommendationInput
                            updateSection={(fieldName, value) => this.props.updateState(fieldName, value)}
                            updateNuminputs={(sectionName, value) => this.props.updateNuminputs(sectionName, value)}
                            numInputs={this.props.numInput.recommendation}
                            inputVals={this.props.inputVal.recommendation}
                            recommendationclass="resume15-recommendation-list"
                            maxInputs={3}
                        />
                    </span>
                </div>
            </>
            )
        else
            return (null)
    }
    render() {
        return (
            <div ref={this.props.refProp} className="resume15Root">
                <div className="resume15Main">
                    <div className="resume15-section-2-P2">
                        <div className="resume15-left-section">
                            {this.renderProject()}

                            {this.renderCertificate()}

                            {this.renderRecommendation()}
                        </div>
                        <div className="resume15-right-section">
                            {this.renderExp()}
                        </div>
                    </div>
                </div>
            </div >
        )
    }
}