import React, { Component } from 'react'
import { Image } from 'react-bootstrap';
import './resumeButtons.css';
import ViewImage from '../../../../assets/images/mini-icons/view.svg';
import EditImage from '../../../../assets/images/mini-icons/edit.svg';
//import { Link } from 'react-router-dom';


export default class ResumeButtons extends Component {
    /*
        Generates View and Edit button for resumeBox component.
        Links to those buttons is received in props from parent.
        props (Received from parent component):
            ViewLink : Link to view the resume
            EditLink : Link to edit the resume
    */
    render() {
        return (
            <div className="resume-button">
                <button style={styles.button} onClick={this.props.ViewLink}>
                    <Image src={ViewImage} className="resume-top-icon" />
                </button>
                <button style={styles.button} onClick={this.props.EditLink}>
                    <Image src={EditImage} className="resume-top-icon" />
                </button>
            </div>
        )
    }
}

const styles = {
    button: {
        backgroundColor: 'rgba(255,255,255,0)',
        border: 'none',
        outline: 'none',
        cursor: 'pointer'
    },
    anchor: {
        outline: 'none'
    }
}
