import React from 'react'
import SimpleSectionComponent from '../simpleSectionComponent';
//import TextareaAutosize from '@material-ui/core/TextareaAutosize';


export default class AchievementInput extends SimpleSectionComponent {

  renderInputs = () => {
    let achievementInputs = [];

    for (let i = 1; i <= this.state.numInputs; i++) {
      achievementInputs.push(
        <input
          style={styles.input}
          ref={i}
          key={i}
          value={this.state.values[i - 1]}
          onChange={(e) => this.updateValue('achievement', e, i - 1)}
          placeholder={placeholders[i - 1]}
          onKeyDown={(e) => this.handleKeyDown('achievement', i, e, i + 1)}
        />
      );
    }
    return achievementInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
      </>
    )
  }
}

let styles = {
  input: {
    resize: 'none'
  }
}

let placeholders = [
  'Won the emerging Excellence Award 2018',
  'Won Marketing Award 2012',
  'Won Director Award 2016',
  'Outstanding Award 2018',
  'Inside Sales Specialist 2019'
]