import React, { Component } from 'react'
import { Modal, Image, Container } from 'react-bootstrap';

export default class ViewModal extends Component {
    /*
        Creates a modal, which pops out when "View Resume" is clicked.
        Input props (from parent):
            showResume      : Bool for modal's visibility toggle
            resumeId        : Id of resume to be shown in modal
            handleToggle()  : Handling the modal visibility (function to toggle 'showResume' param)
            getImage()      : Returns the Image of the resume whose Id is resumeID.
    */
    render() {
        return (
            <Modal style={{ 'backgroundColor': "#000000ba" }} show={this.props.showResume} onHide={() => this.props.handleToggle(this.props.resumeId)} dialogClassName="view-resume" >
                <Modal.Header style={{ height: '0px' }} closeButton></Modal.Header>
                <Modal.Body >
                    <Container>
                        <Image className="resume-viewed" src={this.props.getImage()} />
                        <span onClick={this.props.prev} style={styles.prev}><span aria-hidden="true" className="carousel-control-prev-icon"></span></span>
                        <span onClick={this.props.next} style={styles.next}><span aria-hidden="true" className="carousel-control-next-icon"></span></span>
                    </Container>
                </Modal.Body>
            </Modal>
        )
    }
}


const styles = {
    prev: {
        position: 'absolute',
        top: 0,
        bottom: 0,
        zIndex: 1,
        display: 'flex',
        justifyContent: 'center',
        width: '15%',
        opacity: 0.5,
        marginLeft: '-30%',
        fontSize: '25px',
        cursor: 'pointer',
        alignItems: 'center'
    },
    next: {
        position: 'absolute',
        top: 0,
        bottom: 0,
        zIndex: 1,
        display: 'flex',
        justifyContent: 'center',
        width: "15%",
        opacity: 0.5,
        right: 0,
        marginRight: '-30%',
        cursor: 'pointer',
        alignItems: 'center'
    }
}