import React from 'react';
import Resume19P1 from './resume19P1';
// import Resume19P2 from './resume19P2';
import Master from '../master';
import Editor from '../../editorComponents/editor';

export default class Resume19Main extends Master {

    constructor(props) {
        super(props)
        this.myRef = React.createRef();
        this.state = {
            ...this.state,
        }
    }

    componentDidMount = () => {
        let resume_num = '19'
        this.recieveForm(resume_num)
    }

    incrementExpInputs = () => {
        let currState = this.state.numInput
        if (this.state.numInput.experience < 5) {
            if (this.state.numInput.experience === 2) {
                this.setState({ page2active: true }, this.scrollToPage2)
                currState.experience = this.state.numInput.experience + 1
                this.setState({ numInput: currState })
            } else {
                currState.experience = this.state.numInput.experience + 1
                this.setState({ numInput: currState })
            }
        }
    }

    decrementExpInputs = () => {
        let currState = this.state.numInput
        if (this.state.numInput.experience > 1) {
            currState.experience = this.state.numInput.experience - 1
            this.setState({ numInput: currState })
        }
    }

    render() {
        return (
            <>
                <Editor
                    loading={this.state.loading}
                    submitForm={() => this.submitForm()}
                    buttontoggle={() => this.buttontoggle()}
                    addExperience={() => this.addExperience()}
                    addOptional={(sectionName) => this.addOptional(sectionName)}
                    incrementExpInputs={() => this.incrementExpInputs()}
                    decrementExpInputs={() => this.decrementExpInputs()}
                    page2active={this.state.page2active}
                    paidUser={this.state.paidUser}
                    intern={this.state.numInput.intern}
                    resumePageOne={
                        <Resume19P1
                            updateImage={(e) => this.updateImage(e)}
                            updateBasic={(fieldname, e) => this.updateBasic(fieldname, e)}
                            updateState={(fieldname, value) => this.updateState(fieldname, value)}
                            updateNuminputs={(sectionName, value) => this.updateNuminputs(sectionName, value)}
                            numInput={this.state.numInput}
                            inputVal={this.state.inputVal}
                        />}
                // resumePageTwo={
                //     <Resume19P2
                //         updateState={(fieldname, value) => this.updateState(fieldname, value)}
                //         updateNuminputs={(sectionName, value) => this.updateNuminputs(sectionName, value)}
                //         numInput={this.state.numInput}
                //         inputVal={this.state.inputVal}
                //         refProp={this.myRef}
                //     />}
                />
            </>
        );
    }
}