import React, { Component } from 'react'
import Bounce from "react-reveal/Bounce";
import Flip from "react-reveal/Flip";
import { Image } from 'react-bootstrap'
import ResumeImage from '../../../assets/images/boosted-resume-banner.svg';
// import { Link } from 'react-router-dom';
import './SectionOne.css';

export default class SectionOne extends Component {
    render() {
        return (
            <div className="section-1">
                <div className="textBox-1">
                    <Bounce top>
                        <h1 className="textBox-1-h1">
                            Kickstart Your
                            <span className="highlightText"> Job search </span>
                            With an Eye-catching Professional Resume.
                        </h1>
                    </Bounce>

                    <Bounce left>
                        <p className="textBox-1-paraOne">
                            HiredIn resume builder is specially designed for freshers.
                            Curated and approved by recruiters of fortune 500 companies.
                    </p>
                    </Bounce>
                </div>

                <Flip top>
                    <div className="imageBox-1">
                        <Image
                            className="sectionOne-image"
                            src={ResumeImage}
                        />
                    </div>
                </Flip>
            </div>
        );
    };
};