import React, { Component } from 'react';
import ResumeBox from './modules/resumeBox/resumeBox';
import resumeImage from './data/ResumeImage';
import ViewModal from './modules/Modal/Modal';
import img from '../../assets/images/arrow-down.png';
// import RegisterModal from './modules/Modal/RegisterModal';
// import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import './ResumeList.css';

class ResumeList extends Component {

    constructor(props) {
        super(props)
        this.state = {
            resumeListView: false,
            showRegister: false,
            showResume: false,
            resumeId: null
        }
    }

    toggleShutter = () => {
        this.setState({
            resumeListView: !this.state.resumeListView
        })
    }

    handleToggle = (id = 1) => {
        /*
            Takes input resumeId (Integer)
            Updates the state of component.
            Toggles b/w true and false on alternate clicks of view and hide buttons of Modal.
            and sets Id of resume, on which the button is pressed.
        */
        this.setState({
            showResume: !this.state.showResume,
            resumeId: id
        });
    }

    linkToggle = (id = 1) => {
        // if (this.props.isAuthenticated) {
        //     window.location.href = ("resume-edit-" + (id + 1));
        //     // return (
        //     //     <Link to={"resume-edit-" + id} />
        //     // )
        // }
        // else {
        //     window.location.href = "register";
        //     // this.setState({
        //     //     showRegister: !this.state.showRegister,
        //     //     resumeId: id
        //     // });
        // }
        return window.location.href = ("resume-edit-" + (id + 1));
    }

    getImage = () => {
        /* Returns the image of resume, which is to be shown in modal */
        return resumeImage.data[this.state.resumeId];
    }



    prev = () => {
        if (this.state.resumeId > 0) {
            this.setState({
                resumeId: this.state.resumeId - 1
            });
        }
    }

    next = () => {
        if (this.state.resumeId < 19) {
            this.setState({
                resumeId: this.state.resumeId + 1
            });
        }
    }

    renderResumes = () => {
        /*
            returns list of components of ResumeBox.
            loops through static resume data(template, links)
            and generates list of components to show inside a component.
        */
        let resumeData = [];
        for (let i = 0; i < resumeImage.data.length; i++) {
            resumeData.push(
                <ResumeBox
                    key={i}
                    resumeImage={resumeImage.data[i]}
                    ViewLink={() => this.handleToggle(i)}
                    EditLink={() => this.linkToggle(i)}
                />
            );
        }
        return resumeData;
    }

    render() {
        return (
            <div className="resumeList-main">
                <button className="resumeList-button" onClick={this.toggleShutter}>
                    <span className="resumeList-button-text">
                        Start by Choosing a Template
                        <img
                            className="resumeList-button-img"
                            alt=""
                            src={img}
                            style={this.state.resumeListView ? { 'transform': 'rotateX(180deg)' } : {}}
                        />
                    </span>
                </button>
                <div className="resume-list-background">
                    {this.state.resumeListView ?
                        <div className='resume-list'>
                            {this.renderResumes()}
                        </div> : null}
                </div>
                <ViewModal
                    showResume={this.state.showResume}
                    resumeId={this.state.resumeId}
                    getImage={() => this.getImage()}
                    handleToggle={() => this.handleToggle(this.state.resumeId)}
                    prev={this.prev}
                    next={this.next}
                />
                {/* <RegisterModal
                    showRegister={this.state.showRegister}
                    resumeId={this.state.resumeId + 1}
                    linkToggle={() => this.linkToggle(this.state.resumeId)}
                /> */}
            </div>
        );
    }
}


const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.token !== null
    }
}

export default connect(mapStateToProps, null)(ResumeList);