import { Component } from 'react'

export default class SimpleSectionComponent extends Component {

  componentWillReceiveProps(props) {
    this.setState({ numInputs: props.numInputs })
  }

  // static getDerivedStateFromProps(props) {
  //   return { numInputs: props.numInputs }
  // }

  constructor(props) {
    super(props);
    this.state = {
      numInputs: props.numInputs,
      values: props.inputVals
    }
  }

  updateValue = (sectionName, e, i) => {
    let inputValue = e.target.value;
    let currData = this.state.values;
    currData[i] = inputValue;

    this.setState({
      values: currData
    }, () => {
      this.props.updateSection(sectionName, this.state.values);
    });
  }

  handleKeyDown(sectionName, currIndex, e, nextValue) {
    var KeyID = e.keyCode;
    switch (KeyID) {
      case 13:
        e.preventDefault();
        if (currIndex === this.state.numInputs) {
          if (this.state.numInputs < this.props.maxInputs) {
            this.setState({
              numInputs: this.state.numInputs + 1
            }, () => { this.props.updateNuminputs(sectionName, this.state.numInputs); this.refs[nextValue].focus() })
          }
        }
        else {
          this.refs[nextValue].focus();
        }
        break;
      case 8:
        if (e.target.value === '' && currIndex === this.state.numInputs) {
          if (this.state.numInputs > 1)
            this.setState({
              numInputs: this.state.numInputs - 1
            }, () => { this.props.updateNuminputs(sectionName, this.state.numInputs); this.refs[currIndex - 1].focus() })
        }
        break;
      default:
        break;
    }
  }

}
