import React from 'react'
import SingleSectionComponent from '../singleSectionComponent';


export default class EducationInput extends SingleSectionComponent {

  renderInputs = () => {
    let educationInputs = [];
    educationInputs.push(
      <div key={0} style={styles.inputs}>
        <input
          value={this.state.values.institute}
          onChange={(e) => this.updateValue('education', e, 'institute')}
          placeholder="University of Virginia"
        />
        <input
          style={styles.degree}
          value={this.state.values.degree}
          className={this.props.textCol}
          onChange={(e) => this.updateValue('education', e, 'degree')}
          placeholder="Bachelor in Marketing"
        />
        <input
          value={this.state.values.course}
          onChange={(e) => this.updateValue('education', e, 'course')}
          placeholder="Brand Management"
        />
        <div style={styles.dateInputs}>
          <input
            style={styles.dateInput}
            maxLength='10'
            value={this.state.values.start_date}
            onChange={(e) => this.updateValue('education', e, 'start_date')}
            placeholder="2012"
          />
          <input
            style={styles.dateInput}
            maxLength='10'
            value={this.state.values.end_date}
            onChange={(e) => this.updateValue('education', e, 'end_date')}
            placeholder="2016"
          />
        </div>
        <div>
          <span className={this.props.textCol}>GPA: </span>
          <input
            type="number"
            value={this.state.values.gpa}
            onChange={(e) => this.updateValue('education', e, 'gpa')}
            placeholder="8.5"
          />
        </div>
      </div>
    );
    return educationInputs;
  }

  render() {
    return (
      <>
        {this.renderInputs()}
      </>
    )
  }
}

let styles = {
  inputs: {
    display: 'flex',
    flexDirection: 'column',
  },
  degree: {
    fontSize: '15px',
    fontWeight: '500',
  },
  dateInputs: {
    display: 'flex'
  },
  dateInput: {
    width: '100px',
    marginRight: '10px'
  }
}